import {
  zUniquesItemAttributesIn,
  zUniquesAttribute,
  zUniquesSetAttributeIn,
  zUniquesSetItemMetadataIn,
  zUniquesClearAttributeIn,
  zUniquesClearItemMetadataIn } from
'../../schemas/palletUniques/attributes';
import { ExtrinsicEndpointsConfig, QueryEndpointConfig } from '../_types';
import { queryItem, queryAttributes } from './_queries';
import { zUniquesItemDetails, zUniquesItemsQuery, zUniquesBurnItemQuery, zUniquesMintItem, zUniquesTransferItem } from '../../schemas/palletUniques/item';
import { z } from 'zod';
import { EventNotFoundInExtrinsicError } from '../../schemas/errors';

const mintItem = new ExtrinsicEndpointsConfig({
  route: '/pallet-uniques/item/mint',



  schemaIn: zUniquesMintItem,
  schemaOut: zUniquesItemDetails













});

const transferItem = new ExtrinsicEndpointsConfig({
  route: '/pallet-uniques/item/transfer',



  schemaIn: zUniquesTransferItem,
  schemaOut: zUniquesItemDetails













});

const freezeItem = new ExtrinsicEndpointsConfig({
  route: '/pallet-uniques/item/freeze',



  schemaIn: zUniquesBurnItemQuery,
  schemaOut: zUniquesItemDetails












});

const thawItem = new ExtrinsicEndpointsConfig({
  route: '/pallet-uniques/item/thaw',



  schemaIn: zUniquesBurnItemQuery,
  schemaOut: zUniquesItemDetails












});

const burnItem = new ExtrinsicEndpointsConfig({
  route: '/pallet-uniques/item/burn',



  schemaIn: zUniquesBurnItemQuery,
  schemaOut: z.object({
    collectionId: z.number(),
    itemId: z.number()
  })
















});

const setItemAttribute = new ExtrinsicEndpointsConfig({
  route: '/pallet-uniques/item/set-attribute',






  schemaIn: zUniquesSetAttributeIn.extend({
    itemId: z.coerce.number().openapi({ description: 'The ID of the item.' })
  }),
  schemaOut: zUniquesItemDetails














});

const setItemMetadata = new ExtrinsicEndpointsConfig({
  route: '/pallet-uniques/item/set-metadata',






  schemaIn: zUniquesSetItemMetadataIn,
  schemaOut: zUniquesItemDetails














});

const clearItemAttribute = new ExtrinsicEndpointsConfig({
  route: '/pallet-uniques/item/clear-attribute',






  schemaIn: zUniquesClearAttributeIn.required({ itemId: true }),
  schemaOut: zUniquesItemDetails













});

const clearItemMetadata = new ExtrinsicEndpointsConfig({
  route: '/pallet-uniques/item/clear-metadata',






  schemaIn: zUniquesClearItemMetadataIn,
  schemaOut: zUniquesItemDetails












});

const getItem = new QueryEndpointConfig({
  route: '/pallet-uniques/item/get',


  schemaIn: zUniquesItemsQuery,
  schemaOut: zUniquesItemDetails



});

const getItemAttributes = new QueryEndpointConfig({
  route: '/pallet-uniques/item/attributes',


  schemaIn: zUniquesItemAttributesIn,
  schemaOut: z.object({
    itemId: z.number(),
    attributes: z.array(zUniquesAttribute)
  })








});

export const PalletUniquesItemRoutesConfig = {
  extrinsics: {
    mintItem,
    burnItem,
    transferItem,
    freezeItem,
    thawItem,
    setItemAttribute,
    clearItemAttribute,
    setItemMetadata,
    clearItemMetadata
  },
  queries: {
    getItem,
    getItemAttributes
  }
};