import {z} from 'zod'
import Axios from 'axios'

export class ApiError<T = any> extends Error {
  public readonly isApiError = true

  public readonly data: T
  public readonly zodError: z.ZodError | null

  constructor(axiosError: Error) {
    if (!Axios.isAxiosError(axiosError) || !axiosError.response) {
      const isEconnRefused = (axiosError as any)?.code === 'ECONNREFUSED' || axiosError?.message.includes('ECONNREFUSED')
      if (isEconnRefused) {
        throw new Error('Connection to the server refused, please check the server URL')
      }
      throw new Error(axiosError.message)
    }

    const resp = axiosError.response

    const message =
      `Server responded with Error ${resp.status} - ${resp.statusText}: ` + (typeof resp.data === 'string' ? resp.data : JSON.stringify(resp.data))

    super(message)
    this.data = resp.data
    this.zodError = resp.data.zodError ? new z.ZodError(resp.data.zodError.issues) : null
  }

  static isApiError(err: any): err is ApiError {
    return err.apiErrorFlag
  }
}
