import {
  zUniquesAccountAcceptOwnerQuery,
  zUniquesAccountItems,
  zUniquesAccountItemsQuery,
  zUniquesAccountCollections,
  zUniquesAccountCollectionsQuery } from
'../../schemas/palletUniques/account';
import { ExtrinsicEndpointsConfig, QueryEndpointConfig } from '../_types';
import { queryAccountItemIds, queryAccountCollectionIds } from './_queries';
import { EventNotFoundInExtrinsicError } from '../../schemas/errors';
import { Address } from '@unique-nft/utils';
import { zAddressString } from 'validation-schemas';
import { z } from 'zod';

const acceptCollectionOwnership = new ExtrinsicEndpointsConfig({
  route: '/pallet-uniques/account/accept-collection-ownership',






  schemaIn: zUniquesAccountAcceptOwnerQuery,
  schemaOut: z.object({
    collectionId: z.number().nullable(),
    account: zAddressString
  })












});

const getAccountCollectionIds = new QueryEndpointConfig({
  route: '/pallet-uniques/account/collections',


  schemaIn: zUniquesAccountCollectionsQuery,
  schemaOut: zUniquesAccountCollections








});

const getAccountItemIds = new QueryEndpointConfig({
  route: '/pallet-uniques/account/items',


  schemaIn: zUniquesAccountItemsQuery,
  schemaOut: zUniquesAccountItems








});

export const PalletUniquesAccountRoutesConfig = {
  extrinsics: {
    acceptCollectionOwnership
  },
  queries: {
    getAccountCollectionIds,
    getAccountItemIds
  }
};