import { z } from 'zod';
import { ExtrinsicEndpointsConfig, QueryEndpointConfig, SectionRoutesConfig } from './_types';
import { zAddressString } from 'validation-schemas';
import { Methods } from '../methods';
import { zCollectionIdOrAddress } from '../schemas/collection';
import { TokenEvents } from '../schemas/events';
import {
  zApproveFungibleTokens,
  zApproveFungibleTokensResult,
  zBurnFungibleTokens,
  zBurnFungibleTokensResult,
  zMintFungibleTokenResult,
  zMintFungibleTokens,
  zTransferFungibleTokens,
  zTransferFungibleTokensResult } from
'../schemas/fungible';
import { Address } from '@unique-nft/utils';
import { ApiError } from '../schemas/errors';

const tags = ['Fungible'];

const endpointPrefix = '/fungible';

const mintTokens = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/mint`,


  schemaIn: zMintFungibleTokens,

  schemaOut: zMintFungibleTokenResult




















});

const transferTokensFrom = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/transfer-from`,


  schemaIn: zTransferFungibleTokens,
  schemaOut: zTransferFungibleTokensResult




















});

const burnTokens = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/burn`,


  schemaIn: zBurnFungibleTokens.omit({ from: true }),
  schemaOut: zBurnFungibleTokensResult










});

const burnTokensFrom = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/burn-from`,


  schemaIn: zBurnFungibleTokens,
  schemaOut: zBurnFungibleTokensResult











});

const approveTokens = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/approve`,


  schemaIn: zApproveFungibleTokens,
  schemaOut: zApproveFungibleTokensResult



















});

const getAccountBalance = new QueryEndpointConfig({

  route: `${endpointPrefix}/balance`,
  schemaIn: z.object({
    address: zAddressString,
    collectionId: zCollectionIdOrAddress
  }),
  schemaOut: z.object({
    address: zAddressString,
    collectionId: z.number().int().positive().openapi({ description: 'Collection ID', example: 1 }),
    balance: z.string().openapi({ description: 'Balance of the account', example: '100' }),
    decimals: z.number().int().positive().openapi({ description: 'Decimals of the fungible token', example: 18 }),
    symbol: z.string().openapi({ description: 'Symbol of the fungible token', example: 'DOT' })
  })




















});

const getAccountAllowance = new QueryEndpointConfig({

  route: `${endpointPrefix}/allowance`,
  schemaIn: z.object({
    sender: zAddressString,
    spender: zAddressString,
    collectionId: zCollectionIdOrAddress
  }),
  schemaOut: z.object({
    allowance: z.number().openapi({ description: 'Allowance of the account', example: 100 }),
    address: zAddressString,
    collectionId: z.number().int().positive().openapi({ description: 'Collection ID', example: 1 }),
    sender: zAddressString
  })







});

const getCollectionTotalSupply = new QueryEndpointConfig({

  route: `${endpointPrefix}/total-supply`,
  schemaIn: z.object({
    collectionId: zCollectionIdOrAddress
  }),
  schemaOut: z.object({
    totalSupply: z.number().openapi({ description: 'Total supply of the collection', example: 100 })
  })







});

export const FungibleRoutesConfig = {
  queries: {
    getAccountBalance,
    getAccountAllowance,
    getCollectionTotalSupply
  },
  extrinsics: {
    mintTokens,
    transferTokensFrom,
    burnTokens,
    burnTokensFrom,
    approveTokens
  }
} satisfies SectionRoutesConfig;