import { SchemaTools } from '@unique-nft/schemas';
import { Address } from '@unique-nft/utils/address';
import { Utf8 } from '@unique-nft/utils/string';
import { z } from 'zod';
import 'zod-openapi/extend';
import { zAddressString } from 'validation-schemas';
import {
  zApiQueryTokenId,
  zApiResultToken,
  zApiResultTokenWithChildren,
  zApproveToken,
  zNestToken,
  zTokenPossibleActionsQuery,
  zTokenSchema,
  zTokenURI,
  zUnnestToken } from
'../schemas/token';
import { Methods } from '../methods';
import { TokenEvents } from '../schemas/events';
import { getCollection } from '../methods';
import { ExtrinsicEndpointsConfig, QueryEndpointConfig, SectionRoutesConfig } from './_types';
import { zCollectionIdOrAddress, zPropertiesToEncode } from '../schemas/collection';
import { encodeTokenProperties } from '../utils/token';

const tags = ['Token'];
const endpointPrefix = '/token';

const getToken = new QueryEndpointConfig({

  route: `${endpointPrefix}`,
  schemaIn: zApiQueryTokenId,
  schemaOut: zApiResultTokenWithChildren















});

const mintNftTokens = new ExtrinsicEndpointsConfig({


  route: `${endpointPrefix}/nfts/mint`,



  schemaIn: z.object({
    collectionId: zCollectionIdOrAddress.openapi({ description: 'Collection ID or address', example: 1 }),
    tokens: z.array(
      z.object({
        data: zTokenSchema.optional().openapi({ description: 'Token data' }),
        owner: zAddressString.optional().openapi({ description: 'The owner of the token' }),
        URI: zTokenURI,
        properties: zPropertiesToEncode.optional().openapi({ description: 'The properties of the token' })
      })
    )
  }),

























  schemaOut: z.array(zApiResultToken)







});

const updateNft = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/update`,


  schemaIn: z.object({
    collectionId: zCollectionIdOrAddress.openapi({ description: 'Collection ID or address', example: 1 }),
    tokenId: z.number().int().positive().openapi({ description: 'Token ID', example: 1 }),
    data: zTokenSchema.pick({ attributes: true, image: true, image_details: true }).optional().openapi({ description: 'Token data' })
  }),






















  schemaOut: zApiResultToken







});

const transferTokenFrom = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/transfer-from`,


  schemaIn: z.object({
    collectionId: zCollectionIdOrAddress.openapi({ description: 'Collection ID or address', example: 1 }),
    tokenId: z.number().int().positive().openapi({ description: 'Token ID', example: 1 }),
    amount: z.number().int().positive().optional().openapi({ description: 'Amount of tokens to transfer (for Fungible and Refungible tokens))', example: 1 }),
    from: zAddressString.optional().openapi({ description: 'Sender address (for "transfer from" operations)' }),
    to: zAddressString.openapi({ description: 'Recipient address' })
  }),
  schemaOut: zApiResultToken




















});

const burnToken = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/burn`,


  schemaIn: z.object({
    collectionId: zCollectionIdOrAddress.openapi({ description: 'Collection ID or address', example: 1 }),
    tokenId: z.number().int().positive().openapi({ description: 'Token ID', example: 1 }),
    amount: z.number().int().positive().optional().openapi({ description: 'Amount of tokens to burn (for Fungible and Refungible tokens))', example: 1 })
  }),







  schemaOut: z.object({
    collectionId: z.number().int().positive().openapi({ description: 'Collection ID', example: 1 }),
    tokenId: z.number().int().positive().openapi({ description: 'Token ID', example: 1 }),
    amount: z.number().int().positive().openapi({ description: 'Amount of tokens burned', example: 1 })
  })












});

const burnTokenFrom = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/burn-from`,


  schemaIn: z.object({
    collectionId: zCollectionIdOrAddress.openapi({ description: 'Collection ID or address', example: 1 }),
    tokenId: z.number().int().positive().openapi({ description: 'Token ID', example: 1 }),
    from: zAddressString.optional().openapi({ description: 'Token owner address (for "burn from" operations)' }),
    amount: z.number().int().positive().optional().openapi({ description: 'Amount of tokens to burn (for Fungible and Refungible tokens))', example: 1 })
  }),
















  schemaOut: burnToken.schemaOut

});

const setTokenProperties = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/properties/set`,


  schemaIn: z.object({
    collectionId: zCollectionIdOrAddress.openapi({ description: 'Collection ID or address', example: 1 }),
    tokenId: z.number().int().positive().openapi({ description: 'Token ID', example: 1 }),
    properties: zPropertiesToEncode.openapi({ description: 'The properties of the token' })
  }),












  schemaOut: zApiResultToken







});

const deleteTokenProperties = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/properties/delete`,


  schemaIn: z.object({
    collectionId: zCollectionIdOrAddress.openapi({ description: 'Collection ID or address', example: 1 }),
    tokenId: z.number().int().positive().openapi({ description: 'Token ID', example: 1 }),
    keys: z.array(z.string()).min(1).openapi({ description: 'The keys of the properties to delete' })
  }),





  schemaOut: zApiResultToken







});

const nestToken = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/nest`,


  schemaIn: zNestToken,
  schemaOut: zApiResultToken











});

const unnestToken = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/unnest`,


  schemaIn: zUnnestToken,
  schemaOut: zApiResultToken










});

const approveTokenTransfer = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/approve`,


  schemaIn: zApproveToken,
  schemaOut: TokenEvents.TransferApproved.schema











});

const getApproved = new QueryEndpointConfig({

  route: `${endpointPrefix}/is-approved`,
  schemaIn: z.object({
    collectionId: zCollectionIdOrAddress.openapi({ description: 'Collection ID or address', example: 1 }),
    tokenId: z.coerce.number().int().nonnegative().openapi({ description: 'Token ID', example: 1 }),
    sender: zAddressString.optional().openapi({ description: 'Owner address' }),
    spender: zAddressString.openapi({ description: 'Spender address' })
  }),
  schemaOut: z.object({
    isApproved: z.boolean().openapi({ description: 'Is token approved for transfer (or burn)', example: true }),
    amount: z.string().openapi({ description: 'Approved amount in decimal string', example: '1' })
  })







});

const getTokenPossibleActions = new QueryEndpointConfig({

  route: `${endpointPrefix}/possible-actions`,
  schemaIn: zTokenPossibleActionsQuery,
  schemaOut: z.object({
    burn: z.boolean().openapi({ description: 'Is token burn possible', example: true }),
    nest: z.boolean().openapi({ description: 'Is token nesting possible', example: true }),
    unnest: z.boolean().openapi({ description: 'Is token unnesting possible', example: true }),
    transfer: z.boolean().openapi({ description: 'Is token transfer possible', example: true }),
    changeProperties: z.array(z.string()).openapi({ description: 'List of properties that can be changed', example: ['key1', 'key2'] })
  })

























});

export const TokenRoutesConfig = {
  queries: {
    getToken,
    getApproved,
    getTokenPossibleActions
  },
  extrinsics: {
    mintNftTokens,
    updateNft,
    transferTokenFrom,
    burnToken,
    burnTokenFrom,
    setTokenProperties,
    deleteTokenProperties,
    nestToken,
    unnestToken,
    approveTokenTransfer
  }
} satisfies SectionRoutesConfig;