import {z} from 'zod'

export const maxByteValidation = (maxSize: number) => {
  return (str: string) => {
    const byteSize = new TextEncoder().encode(str).length

    if (byteSize > maxSize) {
      return false
    }
    return true
  }
}

export const zMaxBytesPipe = (maxSize: number) => {
  return z.string().refine(maxByteValidation(maxSize), `String must not exceed ${maxSize} bytes`)
}
