import z from 'zod'

export const zAtBlockHash = z.object({
  atBlockHash: z.string().optional().openapi({description: 'Block hash'}),
})

export type HashOrNumber = {hash: string; number: undefined} | {hash: undefined; number: bigint}

export const zHashOrNumber = z.string().transform<HashOrNumber>((value) => {
  if (value.startsWith('0x') && value.length === 66) return {hash: value}

  return {number: BigInt(value)}
})

export const zAtBlockHashOrNumber = z.object({
  atBlockHashOrNumber: zHashOrNumber.optional(),
})

export const zVestedItem = z.object({
  start: z.string().openapi({description: 'Number of the block when the vesting starts', example: '123456'}),
  period: z.string().openapi({description: 'The duration of the vesting period', example: '1'}),
  periodCount: z.string().openapi({description: 'The number of periods', example: '1'}),
  perPeriod: z.string().openapi({description: 'The amount per period in coins', example: '1.5'}),
})

export const zQueryBoolean = z
  .union([z.string(), z.boolean(), z.number()])
  .transform((val) => {
    if (typeof val === 'string') return val.toLowerCase() === 'true'
    if (typeof val === 'boolean') return val

    return val !== 0
  })
  .openapi({description: 'Boolean query parameter', type: 'boolean'})

export const zPagination = z.object({
  limit: z.coerce.number().int().min(1).max(1000).optional().openapi({description: 'Number of items to return, min 1, max 1000', example: 10}),
  skip: z.coerce.number().int().min(0).optional().openapi({description: 'Number of items to skip, min 0', example: 0}),
})

export type Pagination = z.infer<typeof zPagination>


export const zParsedMetadata = z.object({
  data: z.string().nullable(),
  resolved: z.string().nullable(),
  parsed: z.object({
    name: z.string().optional(),
    description: z.string().optional(),
    image: z.string().optional(),
  })
})

export type ParsedMetadata = z.infer<typeof zParsedMetadata>