import {z} from 'zod'
import {zCollectionIdOrAddress, zDecodedProperty, zPropertiesToEncode} from './collection'
import {zAddressString} from 'validation-schemas'
import {zTokenSchema, zTokenURI} from './token'

export const zRefungibleTokenQuery = z.object({
  collectionId: zCollectionIdOrAddress.openapi({description: 'The ID of the collection.', example: 1}),
  tokenId: z.coerce.number().int().positive().openapi({description: 'Token ID', example: 1}),
})

export const zRefungibleToken = z.object({
  tokenId: z.number().int().positive().openapi({description: 'Token ID', example: 1}),
  collectionId: z.number().int().positive().openapi({description: 'Collection ID', example: 1}),
  owners: z.array(zAddressString.openapi({description: 'The owner of the token.', example: '5GEuzsNk8HkjSDJi1EK91omheS476JAW2PhqrZJSiBrEVeLL'})),
  pieces: z.number().positive().openapi({description: 'The number of pieces in the token.', example: 10_000}),
  properties: z.array(zDecodedProperty),
})

export type RefungibleToken = z.infer<typeof zRefungibleToken>

export const zRefungibleAccountBalanceQuery = z.object({
  collectionId: zCollectionIdOrAddress.openapi({description: 'The ID of the collection.', example: 1}),
  tokenId: z.coerce.number().int().positive().openapi({description: 'Token ID', example: 1}),
  account: zAddressString.openapi({description: 'The owner of the token.', example: '5GEuzsNk8HkjSDJi1EK91omheS476JAW2PhqrZJSiBrEVeLL'}),
})

export const zRefungibleAccountBalance = zRefungibleAccountBalanceQuery.extend({
  collectionId: z.number().int().positive().openapi({description: 'The ID of the collection.', example: 1}),
  balance: z.number().openapi({description: 'The number of pieces in the token.', example: 10_000}),
})

export const zMintRefungibleToken = z.object({
  collectionId: zCollectionIdOrAddress.openapi({description: 'The ID of the collection.', example: 1}),
  owners: z
    .array(
      z.object({
        address: zAddressString.optional(),
        pieces: z.number().int().positive().openapi({description: 'The number of pieces in the token.', example: 10_000}),
      }),
    )
    .openapi({description: 'The owners of the token.', example: [{address: '5GEuzsNk8HkjSDJi1EK91omheS476JAW2PhqrZJSiBrEVeLL', pieces: 10_000}]}),
  properties: zPropertiesToEncode.optional().openapi({description: 'The properties of the token'}),
  URI: zTokenURI,
  data: zTokenSchema.optional().openapi({description: 'Token data'}),
})

export const zMintMultipleRefungibleTokens = z.object({
  collectionId: zCollectionIdOrAddress.openapi({description: 'The ID of the collection.', example: 1}),
  tokens: z.array(
    z.object({
      owner: zAddressString.optional().openapi({description: 'The owner of the token.', example: '5GEuzsNk8HkjSDJi1EK91omheS476JAW2PhqrZJSiBrEVeLL'}),
      data: zTokenSchema.optional().openapi({description: 'Token data'}),
      pieces: z.number().int().positive().openapi({description: 'The number of pieces in the token.', example: 10_000}),
      properties: zPropertiesToEncode.optional().openapi({description: 'The properties of the token'}),
      URI: zTokenURI,
    }),
  ),
})

export const zTransferRefungibleToken = z.object({
  collectionId: zCollectionIdOrAddress.openapi({description: 'The ID of the collection.', example: 1}),
  from: zAddressString.optional().openapi({description: 'The address of the sender.', example: '5GEuzsNk8HkjSDJi1EK91omheS476JAW2PhqrZJSiBrEVeLL'}),
  to: zAddressString.openapi({description: 'The address of the recipient.', example: '5GEuzsNk8HkjSDJi1EK91omheS476JAW2PhqrZJSiBrEVeLL'}),
  tokenId: z.coerce.number().int().positive().openapi({description: 'Token ID', example: 1}),
  pieces: z.number().int().positive().openapi({description: 'The number of pieces in the token.', example: 10_000}),
})

export const zBurnRefungibleToken = z.object({
  collectionId: zCollectionIdOrAddress.openapi({description: 'The ID of the collection.', example: 1}),
  tokenId: z.coerce.number().int().positive().openapi({description: 'Token ID', example: 1}),
  from: zAddressString.optional().openapi({description: 'The address of the sender.', example: '5GEuzsNk8HkjSDJi1EK91omheS476JAW2PhqrZJSiBrEVeLL'}),
  pieces: z.number().int().positive().openapi({description: 'The number of pieces in the token.', example: 5_000}),
})

export const zBurnRefungibleTokenResult = z.object({
  collectionId: z.number().int().positive().openapi({description: 'The ID of the collection.', example: 1}),
  tokenId: z.number().int().positive().openapi({description: 'Token ID', example: 1}),
  pieces: z.number().int().positive().openapi({description: 'The number of pieces in the token.', example: 5_000}),
})

export const zUpdateRefungibleToken = z.object({
  collectionId: zCollectionIdOrAddress.openapi({description: 'Collection ID or address', example: 1}),
  tokenId: z.number().int().positive().openapi({description: 'Token ID', example: 1}),
  data: zTokenSchema.pick({attributes: true, image: true, image_details: true}).optional().openapi({description: 'Token data'}),
})
