import {z} from 'zod'
import 'zod-openapi/extend'
import {zAddressString} from 'validation-schemas'
import {zAtBlockHash} from '../common'
import {zAttribute} from './attributes'
import {zUniversalLocationObject} from 'validation-schemas/xcmSchemas'

export const zNftsCollectionId = z.object({
  collectionId: z.coerce.number().openapi({description: 'Collection ID', example: 1}),
})

export const zPalletNftsCollectionQuery = z
  .object({
    collectionId: z.coerce.number().int().nonnegative().openapi({description: 'Collection ID', example: 1}),
  })
  .merge(zAtBlockHash)

export const zNftsCollectionSettings = z.object({
  lockedTransferItems: z.boolean().openapi({description: 'Locked transfer items', example: false}),
  lockedMetadata: z.boolean().openapi({description: 'Locked metadata', example: false}),
  lockedAttributes: z.boolean().openapi({description: 'Locked attributes', example: false}),
  lockedMaxSupply: z.boolean().openapi({description: 'Locked max supply', example: false}),
})

export type NftsCreateCollectionSettings = z.infer<typeof zNftsCollectionSettings>

const zMintType = z.union([z.literal('Issuer'), z.literal('Public'), z.object({holderOf: z.number()})])

const zNftsItemSettings = z.object({
  lockedTransfers: z.boolean().openapi({description: 'Locked transfers', example: false}),
  lockedMetadata: z.boolean().openapi({description: 'Locked metadata', example: false}),
  lockedAttributes: z.boolean().openapi({description: 'Locked attributes', example: false}),
})

export type NftsItemSettings = z.infer<typeof zNftsItemSettings>

export const zNftsMintSettings = z.object({
  mintType: zMintType.openapi({description: 'Mint type', example: 'Issuer'}),
  price: z.string().or(z.number()).nullable().openapi({description: 'Price', example: '1000000000000000000000'}),
  startBlock: z.number().nullable().openapi({description: 'Start block', example: 1}),
  endBlock: z.number().nullable().openapi({description: 'End block', example: 1}),
  defaultItemSettings: zNftsItemSettings.partial().optional().openapi({description: 'Default item settings'}),
})

export type NftsMintSettings = z.infer<typeof zNftsMintSettings>

export const zNftsCreateCollectionConfig = z.object({
  settings: zNftsCollectionSettings.optional().openapi({description: 'Collection settings'}),
  maxSupply: z.number().optional().openapi({description: 'Max supply', example: 100}),
  mintSettings: zNftsMintSettings.partial().optional().openapi({description: 'Mint settings'}),
})

export const zNftsCollectionCreateArguments = z.object({
  admin: zAddressString.optional(),
  collectionConfig: zNftsCreateCollectionConfig.optional().openapi({description: 'Collection config'}),
})

export const zCollectionCreateResult = z.object({
  collectionId: z.number().openapi({description: 'Collection ID', example: 1}),
  creator: z.string().openapi({description: 'Creator', example: '5GEuzsNk8HkjSDJi1EK91omheS476JAW2PhqrZJSiBrEVeLL'}),
  owner: z.string().openapi({description: 'Owner', example: '5GEuzsNk8HkjSDJi1EK91omheS476JAW2PhqrZJSiBrEVeLL'}),
})

export const zNftsCollectionConfig = z.object({
  settings: zNftsCollectionSettings.optional().openapi({description: 'Collection settings'}),
  maxSupply: z.number().nullable().openapi({description: 'Max supply', example: 100}),
  mintSettings: zNftsMintSettings.partial().optional().openapi({description: 'Mint settings'}),
})

export type NftsCollectionConfigResult = z.infer<typeof zNftsCollectionConfig>

export const zNftsCollectionRole = z.object({
  issuer: z.boolean(),
  admin: z.boolean(),
  freezer: z.boolean(),
})

export type NftsCollectionRole = z.infer<typeof zNftsCollectionRole>

export const zNftsCollectionLockResult = zNftsCollectionId.extend({})

export const zNftsCollectionMetadata = z.object({
  deposit: z.string(),
  data: z.string(),
})

export type NftsCollectionMetadata = z.infer<typeof zNftsCollectionMetadata>

export const zNftsCollectionDetails = z.object({
  collectionId: z.number().openapi({description: 'The ID of the collection', example: 0}),
  owner: z.string().openapi({description: 'The owner of the collection', example: '5GEuzsNk8HkjSDJi1EK91omheS476JAW2PhqrZJSiBrEVeLL'}),
  ownerDeposit: z.number().openapi({description: 'The owner deposit'}),
  items: z.number().openapi({description: 'The number of items'}),
  itemMetadatas: z.number().openapi({description: 'The number of item metadatas'}),
  itemConfigs: z.number().openapi({description: 'The number of item configs'}),
  attributes: z.array(zAttribute).openapi({description: 'Attributes of the collection'}),
  metadata: zNftsCollectionMetadata.nullable().openapi({description: 'The collection metadata'}),
  config: zNftsCollectionConfig.openapi({description: 'The collection config'}),
  universalLocation: zUniversalLocationObject.nullable(),
  isForeign: z.boolean().optional().openapi({description: 'Is foreign collection'}),
  originalUniversalLocation: zUniversalLocationObject.nullable().openapi({description: 'Original collection universal location'}),
})

export type NftsCollectionDetails = z.infer<typeof zNftsCollectionDetails>
