import {z} from 'zod'
import {Address} from '@unique-nft/utils/address'
import {IUniqueSdkSigner} from '@unique-nft/sr25519'
import type {AxiosRequestConfig} from 'axios'

// @ts-ignore
z.ZodType.prototype.openapi = function () {
  return this
}

export type IGetExtrinsicStatusOptions = {
  /**
   * @description Number of retries to get the extrinsic status
   */
  retries?: number

  /**
   * @description Timeout between retries in milliseconds
   */
  timeout?: number

  /**
   * @description Wait for the extrinsic to be included in the finalized block
   * @default false
   */
  isFinalized?: boolean
}

export type UNIQUE_CHAIN_PARAMS = {
  baseUrl: string
  account?: IAccount
  axiosConfig?: AxiosRequestConfig
  statusOptions?: IGetExtrinsicStatusOptions
}

export type IAccount = {signer: IUniqueSdkSigner; address: string}

// todo - moved some schemas and types here. They should be moved to validation-schemas package
// but for now I don't know how to build and bundle it into the thin-client package

export type HexString = `0x${string}`

export interface SignerPayloadJSON {
  /**
   * @description The ss-58 encoded address
   */
  address: string
  /**
   * @description The checkpoint hash of the block, in hex
   */
  blockHash: HexString
  /**
   * @description The checkpoint block number, in hex
   */
  blockNumber: HexString
  /**
   * @description The era for this transaction, in hex
   */
  era: HexString
  /**
   * @description The genesis hash of the chain, in hex
   */
  genesisHash: HexString
  /**
   * @description The encoded method (with arguments) in hex
   */
  method: string
  /**
   * @description The nonce for this transaction, in hex
   */
  nonce: HexString
  /**
   * @description The current spec version for the runtime
   */
  specVersion: HexString
  /**
   * @description The tip for this transaction, in hex
   */
  tip: HexString
  /**
   * @description The current transaction version for the runtime
   */
  transactionVersion: HexString
  /**
   * @description The applicable signed extensions for this runtime
   */
  signedExtensions: string[]
  /**
   * @description The version of the extrinsic we are dealing with
   */
  version: number
}

export interface SignerPayloadRawBase {
  /**
   * @description The hex-encoded data for this request
   */
  data: string
  /**
   * @description The type of the contained data
   */
  type?: 'bytes' | 'payload'
}

export interface SignerPayloadRaw extends SignerPayloadRawBase {
  /**
   * @description The ss-58 encoded address
   */
  address: string
  /**
   * @description The type of the contained data
   */
  type: 'bytes' | 'payload'
}

export interface UnsignedExtrinsicPayload {
  signerPayloadJSON: SignerPayloadJSON
  signerPayloadRaw: SignerPayloadRaw
  signerPayloadHex: string
}

export interface CompactExtrinsicPayload {
  compactExtrinsic: string
}

export interface ExtrinsicFeesPayload {
  baseFee: string
  lenFee: string
  adjustedWeightFee: string
  totalFee: string
  xcmFee: string
}

export interface UnsignedExtrinsicPayloadWithCompactExtrinsic extends UnsignedExtrinsicPayload, CompactExtrinsicPayload {}

export type IGetExtrinsicStatusOutput = {
  hash: string
  signer: string
  section: string
  method: string
  args: Record<string, any>
  events: Array<{section: string; method: string; data: Record<string, any>}>
  groupedEvents: Record<string, Record<string, Array<Record<string, any>>>>
  blockHash: string
  blockNumber: string
}

export const zUniqueBuildExtrinsicOptions = z.object({
  signerAddress: z.string().refine((address) => Address.is.substrateAddress(address)),
  nonce: z.number().optional(),
  mortalLength: z.number().optional(),
})

export type UniqueBuildExtrinsicOptions = z.infer<typeof zUniqueBuildExtrinsicOptions>

export type ConditionalParams<P> = undefined extends P
  ? [params?: P, buildOptions?: UniqueBuildExtrinsicOptions, account?: IAccount, options?: IGetExtrinsicStatusOptions]
  : [params: P, buildOptions?: UniqueBuildExtrinsicOptions, account?: IAccount, options?: IGetExtrinsicStatusOptions]

export type ConditionalEncodeParams<P> = undefined extends P ? [params?: P] : [params: P]
export type ExtrinsicFn<P, K, R> = ((...args: ConditionalParams<P>) => Promise<{
  result: R
  extrinsicOutput: IGetExtrinsicStatusOutput
}>) & {
  route: string
  schemaIn: z.ZodType<any, z.ZodTypeDef, P>
  schemaOut: z.ZodType<R, z.ZodTypeDef, any>
  buildAndSubmit: (...args: ConditionalParams<P>) => Promise<{result: R; extrinsicOutput: IGetExtrinsicStatusOutput}>
  build: (...args: ConditionalParams<P>) => Promise<UnsignedExtrinsicPayloadWithCompactExtrinsic>
  encode: (...args: ConditionalEncodeParams<K>) => Promise<CompactExtrinsicPayload>
  fee: (...args: ConditionalParams<P>) => Promise<ExtrinsicFeesPayload>
  getStatus: (
    endpointPart: string,
    extrinsicHash: string,
    options?: IGetExtrinsicStatusOptions,
  ) => Promise<{result: R; extrinsicOutput: IGetExtrinsicStatusOutput}>
  sign: (payload: UnsignedExtrinsicPayload, account?: IAccount) => Promise<string>
  submit: (payload: UnsignedExtrinsicPayload, signature: string) => Promise<string>
}
