import { z } from 'zod';
import { SubstrateClient } from 'substrate-client';
import { QueryEndpointConfig, SectionRoutesConfig } from '../_types';
import { locationFromObject, parseLocation } from '../../utils/xcm';
import { UniversalLocationObject, zUniversalLocationObject } from 'validation-schemas/xcmSchemas';
import { zPagination } from '../../schemas/common';
import { zAddressString } from 'validation-schemas';
import {
  PalletAssetsAssetDetails,
  PalletAssetsAssetMetadata,
  PalletAssetAssetMetadataType,
  PalletAssetsAssetAccount } from
'../palletAssets/_queries';
import { Address } from '@unique-nft/utils/address';
import { hexToString } from '@polkadot/util/hex';
import { zAsset } from '../../schemas/palletAssets';
import { NotFoundError } from '../../schemas/errors';

const queryDerivativeRegistered = async (
substrateClient: SubstrateClient,
universalLocation: UniversalLocationObject) =>
{
  const transformed = locationFromObject(universalLocation);

  const classRaw = await substrateClient.getStorage('foreignAssets', 'asset', [transformed]);
  const registered = !!classRaw && classRaw !== '0x';

  return { registered };
};

const queryAccountAssetBalance = async (
substrateClient: SubstrateClient,
address: string,
universalLocation: UniversalLocationObject) =>
{
  const transformed = locationFromObject(universalLocation);

  const rawMetadata = await substrateClient.getStorage('foreignAssets', 'metadata', [transformed]);
  const rawBalance = await substrateClient.getStorage('foreignAssets', 'account', [transformed, address]);

  if (!rawMetadata) throw new NotFoundError(`Asset not found: ${JSON.stringify(universalLocation)}`);

  const metadata = substrateClient.registry.createType<PalletAssetsAssetMetadata>(PalletAssetAssetMetadataType, rawMetadata);
  const balance = rawBalance ? substrateClient.registry.createType<PalletAssetsAssetAccount>('PalletAssetsAssetAccount', rawBalance) : null;

  return {
    balance: balance ? balance.balance.toString() : '0',
    symbol: hexToString(metadata.symbol.toString()),
    decimals: metadata.decimals.toNumber()
  };
};

const getDerivativeRegistered = new QueryEndpointConfig({
  route: '/pallet-foreign-assets-asset-hub/registered',

  method: 'POST',
  schemaIn: z.object({
    universalLocation: zUniversalLocationObject
  }),
  schemaOut: z.object({
    registered: z.boolean()
  })



});

const listDerivatives = new QueryEndpointConfig({
  route: '/pallet-foreign-assets-asset-hub/list',

  method: 'GET',
  schemaIn: zPagination,
  schemaOut: z.array(
    zAsset.omit({ id: true })
  )














































});


const getAccountBalance = new QueryEndpointConfig({
  route: '/pallet-foreign-assets-asset-hub/account-items',

  method: 'POST',
  schemaIn: z.object({
    universalLocation: zUniversalLocationObject,
    account: zAddressString
  }),
  schemaOut: z.object({
    balance: z.string(),
    symbol: z.string(),
    decimals: z.number()
  })



});

export const PalletForeignAssetsAssetHubRoutesConfig = {
  queries: {
    getDerivativeRegistered,
    listDerivatives,
    getAccountBalance
  },
  extrinsics: {}
} satisfies SectionRoutesConfig;