import { z } from 'zod';
import 'zod-openapi/extend';
import { zAddressString } from 'validation-schemas';
import { QueryEndpointConfig, SectionRoutesConfig } from './_types';

const getAccountNextIndex = new QueryEndpointConfig({
  route: '/accounts/next_index',

  schemaIn: z.object({ address: zAddressString }),
  schemaOut: z.object({
    nextIndex: z.number().openapi({ description: 'The next index of the account' })
  })








});

export const SystemRoutesConfig = {
  extrinsics: {},
  queries: {
    getAccountNextIndex
  }
} satisfies SectionRoutesConfig;