import { ExtrinsicEndpointsConfig, QueryEndpointConfig, SectionRoutesConfig } from '../_types';
import { z } from 'zod';
import { ApiError, EventNotFoundInExtrinsicError } from '../../schemas/errors';
import {
  zNftsCollectionDetails,
  zPalletNftsCollectionQuery,
  zNftsCollectionCreateArguments,
  zNftsCollectionSettings,
  zNftsMintSettings } from
'../../schemas/palletNfts/collection';
import { Address } from '@unique-nft/utils/address';
import { queryCollectionDetails, queryCollectionsPaginated, queryNextCollectionId } from './_queries';
import { serializeCollectionSettings, serializeItemSettings, serializeNamespace } from './_utils';
import { zAddressString } from 'validation-schemas';
import { stringToHex } from '@polkadot/util';
import { zAttribute } from '../../schemas/palletNfts/attributes';
import { zPagination } from '../../schemas/common';

const getCollection = new QueryEndpointConfig({
  route: '/pallet-nfts/collection/get',


  schemaIn: zPalletNftsCollectionQuery,
  schemaOut: zNftsCollectionDetails






});

const getCollectionsPaginated = new QueryEndpointConfig({
  route: '/pallet-nfts/collections',


  schemaIn: zPagination,
  schemaOut: z.object({
    collections: z.array(zNftsCollectionDetails),
    isLastPage: z.boolean()
  })



});

const getNextCollectionId = new QueryEndpointConfig({
  route: '/pallet-nfts/collection/next-id',


  schemaIn: z.object({}),
  schemaOut: z.object({
    nextCollectionId: z.number()
  })







});

const createCollection = new ExtrinsicEndpointsConfig({
  route: '/pallet-nfts/collection/create',





















  schemaIn: zNftsCollectionCreateArguments.optional(),
  schemaOut: zNftsCollectionDetails.extend({
    creator: zAddressString
  })
































});


const destroyCollection = new ExtrinsicEndpointsConfig({
  route: '/pallet-nfts/collection/destroy',






















  schemaIn: z.object({
    collectionId: z.number().positive(),
    witness: z.
    object({
      itemMetadatas: z.number().default(0),
      itemConfigs: z.number().default(0),
      attributes: z.number().default(0)
    }).
    default({})
  }),












  schemaOut: z.object({
    collectionId: z.number()
  })
});

const acceptCollectionOwnership = new ExtrinsicEndpointsConfig({
  route: '/pallet-nfts/collection/accept-ownership',



  schemaIn: z.object({
    collectionId: z.number().positive().optional()
  }),
  schemaOut: z.object({
    who: z.string(),
    collectionId: z.number().optional()
  })












});

const transferCollectionOwnership = new ExtrinsicEndpointsConfig({
  route: '/pallet-nfts/collection/transfer',















  schemaIn: z.object({
    collectionId: z.number().positive(),
    newOwner: zAddressString
  }),
  schemaOut: zNftsCollectionDetails












});

const lockNftsCollection = new ExtrinsicEndpointsConfig({
  route: '/pallet-nfts/collection/lock',

















  schemaIn: z.object({
    collectionId: z.number().positive(),
    settings: zNftsCollectionSettings.partial().refine((settings) => Object.keys(settings).length > 0, { message: 'At least one setting must be changed' })
  }),
  schemaOut: zNftsCollectionDetails













});

const updateMintSettings = new ExtrinsicEndpointsConfig({
  route: '/pallet-nfts/collection/update-mint-settings',












  schemaIn: z.object({
    collectionId: z.number().positive(),
    mintSettings: zNftsMintSettings.
    partial().
    refine((settings) => Object.keys(settings).length > 0, { message: 'At least one setting must be changed' }).
    refine((settings) => settings.defaultItemSettings ? Object.keys(settings.defaultItemSettings).length > 0 : true, {
      message: 'At least one default item setting must be changed'
    })
  }),
  schemaOut: zNftsCollectionDetails















});

const setMaxSupply = new ExtrinsicEndpointsConfig({
  route: '/pallet-nfts/collection/set-max-supply',













  schemaIn: z.object({
    collectionId: z.number().positive(),
    maxSupply: z.number().positive()
  }),
  schemaOut: zNftsCollectionDetails









});

const setCollectionTeam = new ExtrinsicEndpointsConfig({
  route: '/pallet-nfts/collection/set-team',



















  schemaIn: z.object({
    collectionId: z.number().positive(),
    issuer: zAddressString.optional(),
    admin: zAddressString.optional(),
    freezer: zAddressString.optional()
  }),
  schemaOut: zNftsCollectionDetails.extend({
    role: z.object({
      issuer: zAddressString.nullable(),
      admin: zAddressString.nullable(),
      freezer: zAddressString.nullable()
    })
  })



























});

const setCollectionAttribute = new ExtrinsicEndpointsConfig({
  route: '/pallet-nfts/collection/set-attribute',















  schemaIn: z.object({
    collectionId: z.number().positive(),
    attribute: zAttribute
  }),
  schemaOut: zNftsCollectionDetails




















});

const clearCollectionAttribute = new ExtrinsicEndpointsConfig({
  route: '/pallet-nfts/collection/clear-attribute',















  schemaIn: z.object({
    collectionId: z.number().positive(),
    attribute: zAttribute.omit({ value: true })
  }),
  schemaOut: zNftsCollectionDetails
















});

const setCollectionMetadata = new ExtrinsicEndpointsConfig({
  route: '/pallet-nfts/collection/set-metadata',

















  schemaIn: z.object({
    collectionId: z.number().positive(),
    data: z.string()
  }),
  schemaOut: zNftsCollectionDetails












});

const clearCollectionMetadata = new ExtrinsicEndpointsConfig({
  route: '/pallet-nfts/collection/clear-metadata',












  schemaIn: z.object({
    collectionId: z.number().positive()
  }),
  schemaOut: zNftsCollectionDetails









});

export const PalletNftsCollectionRoutesConfig = {
  queries: {
    getCollection,
    getCollectionsPaginated,
    getNextCollectionId
  },
  extrinsics: {
    createCollection,
    destroyCollection,
    acceptCollectionOwnership,
    transferCollectionOwnership,
    setCollectionMetadata,
    clearCollectionMetadata,
    setCollectionAttribute,
    clearCollectionAttribute,
    lockNftsCollection,
    updateMintSettings,
    setMaxSupply,
    setCollectionTeam
  }
} satisfies SectionRoutesConfig;