import z, {ZodType} from 'zod'
import 'zod-openapi/extend'

const zBaseAbiInput = z.object({
  name: z.string().optional(),
  type: z.string(),
  internalType: z.string().optional(),
  indexed: z.boolean().optional(),
})

type AbiInput = z.infer<typeof zBaseAbiInput> & {components?: AbiInput[]}

export const zAbiInput: ZodType<AbiInput> = zBaseAbiInput.extend({
  components: z
    .lazy(() => zAbiInput.array())
    .optional()
    .openapi({type: 'array'}),
})

export const zAbiItem = z.object({
  name: z.string().optional(),
  inputs: zAbiInput.array().optional(),
  outputs: zAbiInput.array().optional(),
  stateMutability: z.string().optional(),
  type: z.string(),
  payable: z.boolean().optional(),
  constant: z.boolean().optional(),
})

export type AbiItem = z.infer<typeof zAbiItem>

export const zAbi = zAbiItem.array()

export type Abi = z.infer<typeof zAbi>
