import {zAddressString} from 'validation-schemas'
import {z} from 'zod'

export const zUniquesAccountCollections = z.object({
  account: zAddressString.openapi({description: 'The ID of the account', example: '5GEuzsNk8HkjSDJi1EK91omheS476JAW2PhqrZJSiBrEVeLL'}),
  collections: z.array(z.number().openapi({description: 'The ID of the collection'})),
})

export const zUniquesAccountCollectionsQuery = z.object({
  address: z.string().openapi({description: 'The ID of the account', example: '5GEuzsNk8HkjSDJi1EK91omheS476JAW2PhqrZJSiBrEVeLL'}),
})

export type UniquesAccountCollectionQuery = z.output<typeof zUniquesAccountCollectionsQuery>

export const zUniquesAccountItems = z.object({
  account: zAddressString.openapi({description: 'The ID of the account', example: '5GEuzsNk8HkjSDJi1EK91omheS476JAW2PhqrZJSiBrEVeLL'}),
  items: z.array(z.number().openapi({description: 'The ID of the asset'})),
})

export const zUniquesAccountItemsQuery = z.object({
  address: z.string().openapi({description: 'The ID of the account', example: '5GEuzsNk8HkjSDJi1EK91omheS476JAW2PhqrZJSiBrEVeLL'}),
  collectionId: z.coerce.number().int().openapi({description: 'Collection ID', example: 0}),
})

export const zUniquesAccountAcceptOwnerQuery = z.object({
  collectionId: z.coerce.number().int().optional().openapi({description: 'Collection ID', example: 0}),
})

export type UniquesAccountItemsQuery = z.output<typeof zUniquesAccountItemsQuery>
