import { z } from 'zod';
import { zAddressString } from 'validation-schemas';
import type { Option, u32 } from '@polkadot/types-codec';
import { QueryEndpointConfig, SectionRoutesConfig } from '../_types';
import { queryAccountCollectionIds, queryAccountItemIds, queryCollectionRole, queryIsCollectionOwner } from './_queries';
import { zNftsCollectionRole } from '../../schemas/palletNfts/collection';

const getAccountCollections = new QueryEndpointConfig({
  route: '/pallet-nfts/account/collections',


  schemaIn: z.object({
    account: zAddressString
  }),
  schemaOut: z.object({
    account: zAddressString,
    collections: z.number().array()
  })





});

const getCollectionOwnershipAcceptance = new QueryEndpointConfig({
  route: '/pallet-nfts/account/ownership-acceptance',


  schemaIn: z.object({
    account: zAddressString
  }),
  schemaOut: z.object({
    collectionId: z.number().nullable()
  })







});

const getCollectionRole = new QueryEndpointConfig({
  route: '/pallet-nfts/account/collection-role',


  schemaIn: z.object({
    collectionId: z.coerce.number(),
    account: zAddressString
  }),
  schemaOut: zNftsCollectionRole.extend({
    owner: z.boolean()
  })









});

const getAccountItems = new QueryEndpointConfig({
  route: '/pallet-nfts/account/items',


  schemaIn: z.object({
    account: zAddressString,
    collectionId: z.coerce.number().int().nonnegative().openapi({ description: 'Collection ID', example: 1 })
  }),
  schemaOut: z.array(z.object({
    collectionId: z.number(),
    tokenId: z.number(),
    original: z.object({
      collectionId: z.number(),
      tokenId: z.number()
    }).optional()
  }))



});

export const PalletNftsAccountRoutesConfig = {
  queries: {
    getAccountCollections,
    getAccountItems,
    getCollectionOwnershipAcceptance,
    getCollectionRole
  },
  extrinsics: {}
} satisfies SectionRoutesConfig;