import { z } from 'zod';
import 'zod-openapi/extend';
import { ParsedExtrinsic } from 'blocks-storage';
import { ethers } from 'ethers';
import { FastifyInstance } from 'fastify';

import { zAbi } from '../../schemas/evm/abi';
import { endpointPrefix, tags } from './constants';
import { QueryEndpointConfig, QueryHttpMethod } from '../_types';
import { fixBigint } from '../../utils/evm/evmUtils';
import { NotFoundError } from '../../schemas/errors';

const zEvmParseLogs = z.object({
  hash: z.string().openapi({ description: 'Hash of the extrinsic' }),
  isFinalized: z.boolean().optional().openapi({ description: 'Is the block with the extrinsic finalized' }),
  abi: zAbi
});

const zEvmLogEventData = z.object({
  address: z.string().openapi({ description: 'Address of the contract' }),
  topics: z.array(z.string()).openapi({ description: 'Topics of the log' }),
  data: z.string().openapi({ description: 'Data of the log' })
});

const getEvmLogs = (extrinsic: ParsedExtrinsic) => {
  const rawLogs = extrinsic.groupedEvents.evm?.Log as {log: string;}[] || [];

  const logs: z.output<typeof zEvmLogEventData>[] = [];

  for (const { log } of rawLogs) {
    const parsed = zEvmLogEventData.safeParse(JSON.parse(log));

    if (parsed.success) logs.push(parsed.data);
  }

  return logs;
};

const unknownContractParseLogsConfig = {
  tags,
  route: `${endpointPrefix}/contract/logs`,
  schemaIn: zEvmParseLogs,
  schemaOut: z.object({
    logs: z.array(z.any()).openapi({ description: 'Decoded logs' })
  }),
  method: 'POST' as QueryHttpMethod,
  queryHandler: async (fastify: FastifyInstance, query: z.output<typeof zEvmParseLogs>) => {
    const { extrinsicsStorage } = fastify;
    const { hash, abi, isFinalized } = query;

    const extrinsic = await extrinsicsStorage.getExtrinsic(hash, isFinalized);
    if (!extrinsic) throw new NotFoundError(`Extrinsic with hash ${hash} not found`);

    const contractInterface = new ethers.Interface(abi);
    const rawLogs = getEvmLogs(extrinsic);

    const logs: any[] = [];

    for (const rawLog of rawLogs) {
      const log = contractInterface.parseLog(rawLog);

      if (!log) continue;

      const { name, signature, args } = log;
      logs.push(fixBigint({ name, signature, args }));
    }

    return { logs };
  }
};

export const contractLogsRoute = new QueryEndpointConfig(unknownContractParseLogsConfig);