import {SubstrateClient} from 'substrate-client'
import {Pagination} from '../../schemas/common'
import {applyPaginationToStorageKeys, uintKeyComparator} from 'substrate-client/utils/storageUtils'
import {NotFoundError} from '../../schemas/errors'
import {Asset} from '../../schemas/palletAssets'
import {Address} from '@unique-nft/utils/address'
import {hexToString} from '@polkadot/util/hex'
import {getAssetsUniversalLocation} from '../../utils'

import '@polkadot/types-augment'
import type {
  PalletAssetsAssetAccount,
  PalletAssetsAssetDetails,
  PalletAssetsAssetMetadata,
} from '@polkadot/types/lookup'

export type {
  PalletAssetsAssetAccount,
  PalletAssetsAssetDetails,
  PalletAssetsAssetMetadata,
}


const getAssetsIdsPaginated = async (substrateClient: SubstrateClient, query: Pagination) => {
  const {skip = 0, limit = 1000} = query
  const allCollectionsKeys = await substrateClient.getStorageKeysWithMeta<[any]>('assets', 'asset', [])

  const [assetKeys, isLastPage] = applyPaginationToStorageKeys(allCollectionsKeys, uintKeyComparator, {skip, limit})
  const ids = assetKeys.map((key) => key.args[0].toNumber())

  return {ids, isLastPage}
}

export const PalletAssetAssetMetadataType = '{"deposit": "u128", "name": "Bytes", "symbol": "Bytes", "decimals": "u8", "isFrozen": "bool"}'

export const queryAsset = async (substrateClient: SubstrateClient, id: number): Promise<Asset> => {

  const [rawAsset, rawMetadata] = await Promise.all([
    substrateClient.getStorage('Assets', 'asset', id),
    substrateClient.getStorage('Assets', 'metadata', id),
  ])

  if (!rawAsset) throw new NotFoundError(`Asset not found: ${id}`)

  const asset = substrateClient.registry.createType<PalletAssetsAssetDetails>('PalletAssetsAssetDetails', rawAsset);
  const metadata = rawMetadata ? substrateClient.registry.createType<PalletAssetsAssetMetadata>(PalletAssetAssetMetadataType, rawMetadata) : null;

  return {
    id,
    owner: Address.normalize.substrateAddress(asset.owner.toString()),
    issuer: Address.normalize.substrateAddress(asset.issuer.toString()),
    admin: Address.normalize.substrateAddress(asset.admin.toString()),
    freezer: Address.normalize.substrateAddress(asset.freezer.toString()),
    supply: asset.supply.toString(),
    deposit: asset.deposit.toString(),
    minBalance: asset.minBalance.toString(),
    isSufficient: asset.sufficients.toBigInt() > 0n,
    accounts: asset.accounts.toNumber(),
    sufficients: asset.sufficients.toNumber(),
    approvals: asset.approvals.toNumber(),
    status: asset.status.type,
    name: metadata ? hexToString(metadata.name.toString()) : '',
    symbol: metadata ? hexToString(metadata.symbol.toString()) : '',
    decimals: metadata ? metadata.decimals.toNumber() : 0,
    isFrozen: metadata?.isFrozen.isTrue || false,
    universalLocation: getAssetsUniversalLocation(substrateClient, id),
  };
}

export const queryAssetsPaginated = async (substrateClient: SubstrateClient, query: Pagination) => {
  try {
    const {ids, isLastPage} = await getAssetsIdsPaginated(substrateClient, query)
    const assets = await Promise.all(ids.map((id) => queryAsset(substrateClient, id)))

    return {assets, isLastPage}
  } catch (error) {
    console.error('Error querying assets', error)
    throw error
  }
}

export const queryAccountAssetBalance = async (
  substrateClient: SubstrateClient,
  address: string,
  id: number,
) => {
  const rawMetadata = await substrateClient.getStorage('Assets', 'metadata', id)
  const rawBalance = await substrateClient.getStorage('Assets', 'account', [id, address])

  if (!rawMetadata) throw new NotFoundError(`Asset not found: ${id}`)

  const metadata = substrateClient.registry.createType<PalletAssetsAssetMetadata>(PalletAssetAssetMetadataType, rawMetadata);
  const balance = rawBalance ? substrateClient.registry.createType<PalletAssetsAssetAccount>('PalletAssetsAssetAccount', rawBalance) : null;

  return {
    balance: balance ? balance.balance.toString() : '0',
    symbol: hexToString(metadata.symbol.toString()),
    decimals: metadata.decimals.toNumber(),
  }
}
