import {zAddressString} from 'validation-schemas'
import {z} from 'zod'
import {zUniquesAttribute, zUniquesMetadata} from './attributes'
import {zUniversalLocationObject} from 'validation-schemas/xcmSchemas'

export const zUniquesCollectionQuery = z.object({
  collectionId: z.coerce.number().int().openapi({description: 'Collection ID', example: 0}),
})

export type UniquesCollectionQuery = z.infer<typeof zUniquesCollectionQuery>

export const zUniquesCreateCollectionQuery = zUniquesCollectionQuery.extend({
  collectionId: z.coerce.number().int().optional().openapi({description: 'Collection ID', example: 0}),
  admin: zAddressString,
})

export const zUniquesTransferCollectionQuery = zUniquesCollectionQuery.extend({
  newOwner: zAddressString,
})

export const zUniquesCollectionDetails = z.object({
  collectionId: z.coerce.number().int().openapi({description: 'Collection ID', example: 0}),
  owner: z.string().openapi({description: 'The owner of the collection.'}),
  issuer: z.string().openapi({description: 'The issuer of the collection.'}),
  admin: z.string().openapi({description: 'The admin of the collection.'}),
  freezer: z.string().openapi({description: 'The freezer of the collection.'}),
  totalDeposit: z.number().openapi({description: 'The total deposit of the collection.'}),
  freeHolding: z.boolean().openapi({description: 'Whether the collection is free-holding.'}),
  items: z.number().openapi({description: 'The number of items in the collection.'}),
  itemMetadatas: z.number().openapi({description: 'The number of item metadatas in the collection.'}),
  isFrozen: z.boolean().openapi({description: 'Whether the collection is frozen.'}),
  maxSupply: z.coerce.number().int().openapi({description: 'Max supply', example: 0}),
  attributes: z.array(zUniquesAttribute),
  metadata: zUniquesMetadata.nullable().openapi({description: 'The metadata of the collection.'}),
  universalLocation: zUniversalLocationObject.nullable().openapi({description: 'The universal location of the collection'}),
})

export type UniquesCollectionDetails = z.infer<typeof zUniquesCollectionDetails>
