import {Metadata, TypeRegistry, expandMetadata, decorateConstants} from '@polkadot/types'
import {ChainProperties} from '@polkadot/types/interfaces'
import {compactStripLength, u8aToHex, compactAddLength, u8aConcat} from '@polkadot/util'

export {Metadata, TypeRegistry, DecoratedMeta}

import {HexString} from '@unique-nft/utils/string'

import type {DecoratedMeta, Constants, SignerPayloadRaw, SignerPayloadJSON, SignerPayload, UnsignedExtrinsicPayload} from '../types'
import {SystemProperties} from '../jsonRpc/jsonRpc.types'
import {BuildExtrinsic} from 'validation-schemas'

export {SystemProperties}

export type PreparedMetadata = {
  registry: TypeRegistry
  constants: Constants
  decoratedMeta: DecoratedMeta
  metadata: Metadata
}

export const prepareMetadata = (metadataHex: string, systemProperties: SystemProperties): PreparedMetadata => {
  const registry = new TypeRegistry()
  const metadata = new Metadata(registry, metadataHex as `0x${string}`)
  const constants = decorateConstants(registry, metadata.asLatest, metadata.version)

  registry.setMetadata(metadata)
  const chainProperties = registry.createType('ChainProperties', systemProperties) as ChainProperties
  registry.setChainProperties(chainProperties)

  const decoratedMeta = expandMetadata(registry, metadata)

  return {registry, decoratedMeta, metadata, constants}
}

export const getCompactExtrinsic = (version: number, methodHex: `0x${string}`) => {
  const asU8a = u8aConcat([version], methodHex)
  const withLength = compactAddLength(asU8a)

  return u8aToHex(withLength)
}

export const generateRegistryUtils = (typeRegistry: TypeRegistry) => {
  const createEra = (
    startBlock: number | string,
    desiredPeriod: number | string,
  ) => {
    const _desiredPeriod = typeof desiredPeriod === 'string' ? parseInt(desiredPeriod, 10) : desiredPeriod
    // period must be a power of 2, here we want to round up to the next power of 2
    const period = Math.pow(2, Math.ceil(Math.log2(_desiredPeriod)));

    if (period < 4 || period > 65536) throw new Error('Period must be between 4 and 65536');

    return typeRegistry.createTypeUnsafe('ExtrinsicEra', [{
      current: startBlock,
      period
    }]);
  };

  const createSignerPayload = (signatureOptions: unknown, signerAddress: string, blockNumber: string, method: unknown, version: unknown) => {
    return typeRegistry.createTypeUnsafe('SignerPayload', [
      Object.assign({}, signatureOptions, {
        address: signerAddress,
        blockNumber,
        method,
        version,
      }),
    ])
  }

  const getSignerPayloadHex = (signerPayloadRaw: SignerPayloadRaw): string => {
    const rawPayloadDataU8a = HexString.toU8a(signerPayloadRaw.data)

    if (rawPayloadDataU8a.length > 256) {
      const payloadHash = typeRegistry.hash(rawPayloadDataU8a)

      return HexString.fromU8a(payloadHash)
    }

    return signerPayloadRaw.data
  }

  const createSignerPayloadHex = (signerPayloadRaw: SignerPayloadRaw): string => {
    const rawPayloadDataU8a = HexString.toU8a(signerPayloadRaw.data)

    if (rawPayloadDataU8a.length > 256) {
      const payloadHash = typeRegistry.hash(rawPayloadDataU8a)

      return HexString.fromU8a(payloadHash)
    }

    return signerPayloadRaw.data
  }

  const createSignerPayloadRaw = (signerPayloadJSON: SignerPayloadJSON): SignerPayloadRaw => {
    const rawPayloadDataU8a = typeRegistry
      .createType('ExtrinsicPayload', signerPayloadJSON, {
        version: signerPayloadJSON.version,
      })
      .toU8a({method: true})

    return {
      address: signerPayloadJSON.address,
      data: HexString.fromU8a(rawPayloadDataU8a),
      type: 'payload',
    }
  }

  const signerPayloadToUnsignedExtrinsicPayload = (signerPayload: SignerPayload): UnsignedExtrinsicPayload => {
    const signerPayloadJSON = {
      address: signerPayload.address.toString(),
      blockHash: signerPayload.blockHash.toHex(),
      blockNumber: signerPayload.blockNumber.toHex(),
      era: signerPayload.era.toHex(),
      genesisHash: signerPayload.genesisHash.toHex(),
      method: signerPayload.method.toHex(),
      nonce: signerPayload.nonce.toHex(),
      specVersion: signerPayload.runtimeVersion.specVersion.toHex(),
      tip: signerPayload.tip.toHex(),
      transactionVersion: signerPayload.runtimeVersion.transactionVersion.toHex(),
      signedExtensions: signerPayload.signedExtensions.map((e) => e.toString()),
      version: signerPayload.version.toNumber(),
    }
    const signerPayloadRaw = signerPayload.toRaw()
    const signerPayloadHex = getSignerPayloadHex(signerPayloadRaw)

    return {
      signerPayloadJSON,
      signerPayloadRaw,
      signerPayloadHex,
    }
  }

  return {
    createEra,
    createSignerPayload,
    createSignerPayloadHex,
    createSignerPayloadRaw,
    signerPayloadToUnsignedExtrinsicPayload,
  }
}

const toCamelCase = (str: string) => {
  const replaced = str.replace(/_([a-z])/g, (g) => g[1].toUpperCase())

  return replaced.charAt(0).toLowerCase() + replaced.slice(1)
}

const getDecoratedStorageEntry = (decoratedMeta: DecoratedMeta, section: string, method: string) => {
  const _section = toCamelCase(section)
  const _method = toCamelCase(method)

  if (!decoratedMeta.query[_section]) throw new Error(`Unknown section: ${section}`)
  if (!decoratedMeta.query[_section][_method]) throw new Error(`Unknown method: ${section}.${method}`)

  return decoratedMeta.query[_section][_method]
}

const getDecoratedCall = (decoratedMeta: DecoratedMeta, section: string, method: string) => {
  const callSection = decoratedMeta.tx[section]
  if (!callSection) throw new Error(`Unknown extrinsic section: ${section}`)

  const callMethod = callSection[method]
  if (!callMethod) throw new Error(`Unknown extrinsic method: ${section}.${method}`)

  return callMethod
}

export const encodeStorageKey = (
  decoratedMeta: DecoratedMeta,
  section: string,
  method: string,
  args?: unknown | unknown[],
  iteratorKey = false,
): string => {
  const storageEntry = getDecoratedStorageEntry(decoratedMeta, section, method)
  const _args = Array.isArray(args) ? args : [args]

  if (!iteratorKey) {
    const storageKeyBytes = storageEntry(..._args)
    const [_, keyU8a] = compactStripLength(storageKeyBytes)

    return u8aToHex(keyU8a)
  }

  if (!storageEntry.iterKey) throw new Error(`Method ${section}.${method} does not support iteration`)
  const iteratorKeyBytes = storageEntry.iterKey(..._args)

  return u8aToHex(iteratorKeyBytes)
}

export const encodeMethod = <A extends Record<string, any>>(
  decoratedMeta: DecoratedMeta,
  buildArguments: Pick<BuildExtrinsic, 'section' | 'method' | 'args'>,
): `0x${string}` => {
  const {section, method} = buildArguments
  const callMethod = getDecoratedCall(decoratedMeta, section, method)

  const args = buildArguments.args as A
  const argsArray: unknown[] = []

  callMethod.meta.args.forEach((field, index) => {
    const value = args[field.name.toString()] === undefined ? args[index] : args[field.name.toString()]
    if (value === undefined) throw new Error(`${section}.${method} expects argument "${field.name.toString()}, index: ${index}"`)

    argsArray.push(value)
  })

  const encoded = callMethod(...argsArray)

  return encoded.toHex()
}
