import { UtfHelpers } from '@unique-nft/utils/string';
import { z } from 'zod';
import 'zod-openapi/extend';
import { ExtrinsicEndpointsConfig, QueryEndpointConfig, SectionRoutesConfig } from '../_types';
import { zAttribute, zAttributeNamespace } from '../../schemas/palletNfts/attributes';
import { deserializeNamespace, serializeNamespace } from './_utils';
import { EventNotFoundInExtrinsicError } from '../../schemas/errors';
import { queryAttributes, queryAttribute, queryAttributesAllNamespaces } from './_queries';

const getAttribute = new QueryEndpointConfig({
  route: '/pallet-nfts/attribute',


  schemaIn: z.object({
    collectionId: z.coerce.number().positive(),
    itemId: z.coerce.number().positive().optional(),
    namespace: zAttributeNamespace,
    key: z.string()
  }),
  schemaOut: z.object({
    collectionId: z.number().positive(),
    itemId: z.number().positive().optional(),
    attribute: zAttribute.nullable()
  })





});

const getAttributes = new QueryEndpointConfig({
  route: '/pallet-nfts/attributes',


  schemaIn: z.object({
    collectionId: z.coerce.number().positive(),
    itemId: z.coerce.number().positive().optional(),
    namespace: zAttributeNamespace.optional()
  }),
  schemaOut: z.any()







});

const zSetAttributeIn = z.object({
  collectionId: z.number().positive(),
  itemId: z.number().positive().optional(),
  attribute: zAttribute
});

const setAttribute = new ExtrinsicEndpointsConfig({
  route: '/pallet-nfts/set-attribute',
























  schemaIn: zSetAttributeIn,
  schemaOut: zSetAttributeIn




























});

const zClearAttributeIn = z.object({
  collectionId: z.number().positive(),
  itemId: z.number().positive().optional(),
  attribute: zAttribute.omit({ value: true })
});

const clearAttribute = new ExtrinsicEndpointsConfig({
  route: '/pallet-nfts/clear-attribute',



















  schemaIn: zClearAttributeIn,
  schemaOut: zClearAttributeIn























});

export const PalletNftsAttributesRoutesConfig = {
  queries: {
    getAttribute,
    getAttributes
  },
  extrinsics: {
    setAttribute,
    clearAttribute
  }
} satisfies SectionRoutesConfig;