import { z } from 'zod';
import { QueryEndpointConfig, SectionRoutesConfig } from './_types';
import { zCollectionIdOrAddress } from '../schemas/collection';
import { decodeProperties } from '../utils/collection';
import { RawProperty } from '../schemas/types';
import { zParsedMetadata } from '../schemas/common';

const getTokenParsedMetadata = new QueryEndpointConfig({
  route: '/utils/parsed-metadata',

  schemaIn: z.object({
    collectionId: zCollectionIdOrAddress.openapi({ description: 'Collection ID or address', example: 1 }),
    tokenId: z.coerce.number().int().positive().openapi({ description: 'Token ID', example: 1 })
  }),
  schemaOut: zParsedMetadata

















});

export const UtilsRoutesConfig = {
  queries: {
    getTokenParsedMetadata
  },
  extrinsics: {}
} satisfies SectionRoutesConfig;