import {z} from 'zod'
import 'zod-openapi/extend'
import {zSubstrateAddress} from 'validation-schemas'

// excluding "Account" from the enum, because we assume valid address value to be "Account" by default
export enum AttributeNamespaceEnum {
  pallet = 'pallet',
  collectionOwner = 'collectionOwner',
  itemOwner = 'itemOwner',
}

export const zAttributeNamespace = z
  .union([z.nativeEnum(AttributeNamespaceEnum), zSubstrateAddress])
  .openapi({description: 'Namespace, which can be either a named namespace or an account address'})

export const zAttribute = z.object({
  key: z.string().openapi({description: 'Attribute key'}),
  value: z.string().openapi({description: 'Attribute value'}),
  namespace: zAttributeNamespace,
})

export type AttributeNamespace = z.output<typeof zAttributeNamespace>
export type Attribute = z.output<typeof zAttribute>

export const zGetAttributesQuery = z.object({
  collectionId: z.coerce.number().nonnegative(),
  itemId: z.coerce.number().nonnegative().optional(),
  namespace: zAttributeNamespace,
})

export type GetAttributesQuery = z.output<typeof zGetAttributesQuery>

export const zGetAttributeQuery = zGetAttributesQuery.extend({
  key: z.string().openapi({description: 'Attribute key'}),
})

export type GetAttributeQuery = z.output<typeof zGetAttributeQuery>
