import { z } from 'zod';
import 'zod-openapi/extend';
import { zEthereumAddress } from 'validation-schemas';
import { getContractExists, getGasPrice } from '../../utils/evm/handlers';
import { QueryEndpointConfig, ExtrinsicEndpointsConfig } from '../_types';
import { DEFAULT_GAS_LIMIT, endpointPrefix, tags } from './constants';
import { encodeValue, toEthereumFormat } from '../../utils/evm/evmUtils';
import { EvmEvents } from '../../schemas/evm';

export const zEvmDeployArgs = z.object({
  bytecode: z.string().regex(/^0x[0-9a-fA-F]*$/, { message: 'Bytecode of the contract, must start with 0x' }),
  value: z.coerce.bigint().optional().openapi({ description: 'Value to send with the transaction', type: 'string' }),
  gasLimit: z.coerce.bigint().optional().openapi({ description: 'Gas limit for the transaction', type: 'string' }),
  maxFeePerGas: z.coerce.bigint().optional().openapi({ description: 'Maximum fee per gas', type: 'string' }),
  maxPriorityFeePerGas: z.coerce.bigint().optional().openapi({ description: 'Maximum priority fee per gas', type: 'string' }),
  nonce: z.number().optional().openapi({ description: 'Nonce of the transaction' }),
  accessList: z.array(zEthereumAddress).optional().openapi({ description: 'Access list' }),
  source: z.string().optional().openapi({ description: 'Source address' })
});

export const contractExistsRoute = new QueryEndpointConfig({

  route: `${endpointPrefix}/contract/exists`,
  schemaIn: z.object({
    address: zEthereumAddress
  }),
  schemaOut: z.object({
    exists: z.boolean().openapi({ description: 'Flag that defines whether the contract exists' })
  })





});

export const contractDeployRoute = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/contract/deploy`,


  schemaIn: zEvmDeployArgs,



















  schemaOut: z.object({
    contractAddress: z.string().openapi({ description: 'Address of the created contract' })
  })



});