import {Address} from '@unique-nft/utils/address'
import {SubstrateClient} from 'substrate-client'
import {HashOrNumber} from '../../schemas/common'

export const getBlockNumber = async (substrateClient: SubstrateClient, at: HashOrNumber | undefined) => {
  if (!at) return undefined

  if (at.number) return at.number.toString(10)

  if (at.hash) {
    const blockNumber = await substrateClient.getBlockNumber(at.hash)

    if (!blockNumber) throw new Error(`Block with hash ${at.hash} not found`)

    return blockNumber.toString(10)
  }

  return undefined
}

export const toEthereumFormat = (address: string) => {
  if (Address.is.ethereumAddress(address)) return address

  return Address.mirror.substrateToEthereum(address)
}

export const encodeValue = (value?: number | bigint | string) => {
  if (!value) return '0x0'

  return typeof value === 'string' ? value : `0x${value.toString(16)}`
}

export const fixBigint = (input: unknown) => {
  const asString = JSON.stringify(input, (_, value) => (typeof value === 'bigint' ? value.toString() : value))

  return JSON.parse(asString)
}
