import {zAddressString} from 'validation-schemas'
import {z} from 'zod'
import {zUniquesAttribute, zUniquesMetadata} from './attributes'

export const zUniquesItem = z.object({
  itemId: z.coerce.number().int().openapi({description: 'Item ID', example: 0}),
  collectionId: z.coerce.number().int().openapi({description: 'Collection ID', example: 0}),
  owner: z.string().openapi({description: 'The owner of the item'}),
  approved: z.string().nullable().openapi({description: 'Approved address'}),
  isFrozen: z.boolean().openapi({description: 'Whether the item is frozen'}),
  deposit: z.number().openapi({description: 'The deposit of the item'}),
})

export const zUniquesItemDetails = zUniquesItem.extend({
  metadata: zUniquesMetadata.nullable().openapi({description: 'The metadata of the item'}),
  attributes: z.array(zUniquesAttribute),
})

export type UniquesItemDetails = z.output<typeof zUniquesItemDetails>

export const zUniquesItemsQuery = z.object({
  collectionId: z.coerce.number().int().openapi({description: 'Collection ID', example: 0}),
  itemId: z.coerce.number().int().openapi({description: 'Item ID', example: 0}),
})

export type UniquesItemQuery = z.output<typeof zUniquesItemsQuery>

export const zUniquesCollectionItemsQuery = zUniquesItemsQuery.omit({itemId: true})

export type UniquesCollectionItemsQuery = z.output<typeof zUniquesCollectionItemsQuery>

export const zUniquesMintItem = zUniquesItemsQuery.extend({
  owner: zAddressString.openapi({description: 'The owner of the item', example: '5GEuzsNk8HkjSDJi1EK91omheS476JAW2PhqrZJSiBrEVeLL'}),
})

export const zUniquesItemMetadataQuery = zUniquesItemsQuery

export type UniquesItemMetadataQuery = z.output<typeof zUniquesItemMetadataQuery>

export const zUniquesCollectionMetadataQuery = zUniquesItemsQuery.omit({itemId: true})

export type UniquesCollectionMetadataQuery = z.output<typeof zUniquesCollectionMetadataQuery>

export const zUniquesTransferItem = zUniquesItemsQuery.extend({
  newOwner: zAddressString.openapi({description: 'The new owner of the item', example: '5GEuzsNk8HkjSDJi1EK91omheS476JAW2PhqrZJSiBrEVeLL'}),
  oldOwner: zAddressString.optional().openapi({description: 'The old owner of the item', example: '5GEuzsNk8HkjSDJi1EK91omheS476JAW2PhqrZJSiBrEVeLL'}),
})

export const zUniquesBurnItemQuery = zUniquesItemsQuery.extend({
  checkOwner: zAddressString.optional().openapi({description: 'The owner of the item', example: '5GEuzsNk8HkjSDJi1EK91omheS476JAW2PhqrZJSiBrEVeLL'}),
})
