function toHexString(uint8Array: Uint8Array): string {
  return (
    '0x' +
    Array.from(uint8Array)
      .map((b) => b.toString(16).padStart(2, '0'))
      .join('')
  )
}

const MAX_SAFE_INTEGER = BigInt(Number.MAX_SAFE_INTEGER)

function replacer(_: string, value: any): any {
  if (typeof value === 'bigint') {
    return MAX_SAFE_INTEGER >= value ? Number(value) : value.toString() + 'n'
  }

  if (value instanceof Uint8Array) return toHexString(value)

  return value
}

function reviver(_: string, value: any): any {
  if (typeof value === 'string' && /^\d+n$/.test(value)) {
    return BigInt(value.slice(0, -1)) // "123n" -> bigint
  }

  if (typeof value === 'number' && Number.isInteger(value)) {
    return BigInt(value)
  }

  return value
}

export function jsonStringifyXCM(original: unknown) {
  return JSON.stringify(original, replacer)
}

export function jsonParseXCM<T>(json: string, sanitize?: (value: T) => void) {
  const parsed = JSON.parse(json, reviver) as T

  if (sanitize) sanitize(parsed)

  return parsed
}

export function parseXCM<T>(value: unknown, sanitize?: (value: T) => void) {
  if (typeof value === 'string') {
    return jsonParseXCM(value, sanitize) as T
  }

  return jsonParseXCM(jsonStringifyXCM(value), sanitize) as T
}

export function parseNetworkId<N>(envVar: string | undefined): N | null {
  if (!envVar) return null

  try {
    if (envVar.startsWith('{')) {
      const parsed = JSON.parse(envVar)
      if ('byGenesis' in parsed || 'byFork' in parsed || 'ethereum' in parsed) {
        return parsed as N
      }
    }

    const validSimpleIds = ['polkadot', 'kusama', 'bitcoinCore', 'bitcoinCash', 'polkadotBulletin']
    if (validSimpleIds.includes(envVar)) return envVar as N
  } catch (e) {
    console.error('Invalid NETWORK_ID:', envVar, e)
  }

  return null
}
