import {z} from 'zod'
import {Address} from '@unique-nft/utils/address'
import 'zod-openapi/extend'

export const zSubstrateAddress = z
  .string()
  .refine((address) => Address.is.substrateAddress(address))
  .openapi({
    description: 'Substrate address',
    example: '5DvpvbetLdgBfbb4S5sZ1CCGDWeLDhLmX5WXHd73LjsB2E2v',
  })

export const zEthereumAddress = z
  .string()
  .refine((address) => Address.is.ethereumAddress(address))
  .openapi({
    description: 'Ethereum address',
    example: '0x52764bbc320189511E826A0a9b4763F104FfC3B0',
  })

export const zAddressString = z
  .string()
  .refine((address) => {
    return Address.is.validAddressInAnyForm(address)
  })
  .openapi({
    description: 'Any Address: Ethereum address or Substrate address or Substrate public key',
    example: '5DvpvbetLdgBfbb4S5sZ1CCGDWeLDhLmX5WXHd73LjsB2E2v',
    minLength: 42,
    maxLength: 66,
  })

export const zWeiString = z.string().openapi({
  description: 'The amount in wei',
  example: '1500000000000000000',
})
export const zCoinsString = z.string().openapi({
  description: 'The amount in coins',
  example: '1.5',
})

export const zExtrinsicEraEncoded = z.string().openapi({
  description: 'The era for this transaction, in hex',
  example: '0x9503',
})

export const zExtrinsicMethodEncoded = z.string().openapi({
  description: 'The method for this transaction, in hex',
  example: '0x1e03008ea22863f6d84b1f76e4377c1ba1f6c8bd75b0d3bb5d11c36c0436bdd3110867e5c0',
})

export const zSignerPayloadJSON = z.object({
  address: zSubstrateAddress.openapi({
    description: 'The address of the signer',
    example: '5DvpvbetLdgBfbb4S5sZ1CCGDWeLDhLmX5WXHd73LjsB2E2v',
  }),
  blockNumber: z.string().openapi({
    description: 'The block number of the chain',
    example: '0x000c9c52',
  }),
  era: zExtrinsicEraEncoded,
  method: zExtrinsicMethodEncoded,
  nonce: z.string().openapi({
    description: 'The nonce for this transaction, in hex',
    example: '0x00000000',
  }),
  version: z.number(),
  blockHash: z.string().openapi({
    description: 'The block hash of the chain',
    example: '0xabb0ec38ebeaeae36dbad82cfb33a8e16b7d18a5f937543d5d80d845ef450329',
  }),
  genesisHash: z.string().openapi({
    description: 'The genesis hash of the chain',
    example: '0x49cc5821aa055c3ee9357fe109219230e9ee98792c2e26a0f416233f80a2b5d7',
  }),
  specVersion: z.string().openapi({
    description: 'The current spec version for the runtime',
    example: '0x00990bf6',
  }),
  tip: z.string().openapi({
    description: 'The tip for this transaction, in hex',
    example: '0x00000000000000000000000000000000',
  }),
  transactionVersion: z.string().openapi({
    description: 'The current transaction version for the runtime',
    example: '0x00000003',
  }),
  signedExtensions: z.array(z.string()).openapi({
    description: 'The applicable signed extensions for this runtime',
    example: ['CheckSpecVersion', 'CheckTxVersion', 'CheckGenesis'],
  }),
})

export type SignerPayloadJSON = z.infer<typeof zSignerPayloadJSON>

export const zSignerPayloadRaw = z.object({
  address: zSubstrateAddress.openapi({
    description: 'The address of the signer',
  }),
  data: z.string().openapi({
    description: 'The raw data of the signer payload, in hex',
  }),
  type: z.string(),
})

// todo - write about difference between signerPayloadHex and signerPayloadRaw
export const zSignerPayloadHex = z.string().openapi({
  description: 'The signer payload, in hex',
})

export const zUniqueBuildExtrinsicOptions = z.object({
  signerAddress: zSubstrateAddress,
  nonce: z.number().optional(),
  mortalLength: z.number().optional(),
})
export type UniqueBuildExtrinsicOptions = z.infer<typeof zUniqueBuildExtrinsicOptions>
