import {type ZodType, type output, type input} from 'zod'
import type {ParsedExtrinsic} from 'blocks-storage'
import type {FastifyInstance} from 'fastify'
import type {SubstrateClient} from 'substrate-client'

export type BuildArgsFormatterParams = {signerAddress: string}

export type BuildArgsFormatter<SchemaIn, FormattedArgs = any> = (
  args: SchemaIn,
  params: BuildArgsFormatterParams,
  substrateClient: SubstrateClient,
) => FormattedArgs | Promise<FormattedArgs>

export type EncodeArgsFormatter<SchemaIn, FormattedArgs = any> = (args: SchemaIn, substrateClient: SubstrateClient) => FormattedArgs | Promise<FormattedArgs>

type RouteType = 'build' | 'encode' | 'status' | 'fee' | 'unknown'

export class ExtrinsicEndpointsConfig<
  SchemaIn extends ZodType,
  SchemaOut extends ZodType,
  EncodeSchemaIn extends ZodType = SchemaIn,
  A = any,
  OptionsSchema extends ZodType = ZodType<any>,
> {
  public route: string
  public tags: string[]
  public description?: string
  public extrinsicSection: string
  public extrinsicMethod: string
  public schemaIn: SchemaIn
  public encodeSchemaIn: EncodeSchemaIn
  public buildArgsFormatter: BuildArgsFormatter<output<SchemaIn>, A>
  public encodeArgsFormatter: EncodeArgsFormatter<output<SchemaIn>, A>
  public schemaOut: SchemaOut
  public resultFormatter: (extrinsic: ParsedExtrinsic, fastify: FastifyInstance) => Promise<input<SchemaOut>>
  public routeType: RouteType = 'unknown'
  constructor(config: {
    route: string
    extrinsicSection: string
    extrinsicMethod: string
    tags: string[]
    description?: string
    schemaIn: SchemaIn
    buildArgsFormatter: BuildArgsFormatter<output<SchemaIn>, A>
    encodeArgsFormatter?: EncodeArgsFormatter<output<SchemaIn>, A>
    schemaOut: SchemaOut
    resultFormatter: (extrinsic: ParsedExtrinsic, fastify: FastifyInstance) => Promise<input<SchemaOut>>
    encodeSchemaIn?: EncodeSchemaIn
    routeType?: RouteType
  }) {
    this.route = config.route
    this.tags = config.tags
    this.description = config.description
    this.extrinsicSection = config.extrinsicSection
    this.extrinsicMethod = config.extrinsicMethod
    this.schemaIn = config.schemaIn
    this.buildArgsFormatter = config.buildArgsFormatter

    this.encodeSchemaIn = (config.encodeSchemaIn ?? config.schemaIn) as EncodeSchemaIn

    this.encodeArgsFormatter =
      config.encodeArgsFormatter ??
      async function (args, client) {
        return config.buildArgsFormatter(args, {signerAddress: ''}, client)
      }
    this.schemaOut = config.schemaOut
    this.resultFormatter = config.resultFormatter
    this.routeType = config.routeType || this.routeType
  }
}

export type QueryHttpMethod = 'GET' | 'POST'

export class QueryEndpointConfig<SchemaIn extends ZodType, SchemaOut extends ZodType> {
  public route: string
  public method: QueryHttpMethod
  public tags: string[]
  public description?: string
  public schemaIn: SchemaIn
  public schemaOut: SchemaOut
  public queryHandler: (fastify: FastifyInstance, query: output<SchemaIn>) => Promise<input<SchemaOut>>

  constructor(config: {
    route: string
    tags: string[]
    description?: string
    schemaIn: SchemaIn
    schemaOut: SchemaOut
    queryHandler: (fastify: FastifyInstance, query: output<SchemaIn>) => Promise<input<SchemaOut>>
    method?: QueryHttpMethod
  }) {
    this.route = config.route
    this.tags = config.tags
    this.description = config.description
    this.schemaIn = config.schemaIn
    this.schemaOut = config.schemaOut
    this.queryHandler = config.queryHandler
    this.method = config.method || 'GET'
  }
}

export type SectionRoutesConfig = {
  extrinsics: Record<string, ExtrinsicEndpointsConfig<any, any, any>>
  queries: Record<string, QueryEndpointConfig<any, any>>
}
