import { Address } from '@unique-nft/utils';
import { ApiError, EventNotFoundInExtrinsicError } from '../../schemas/errors';
import { zUniquesItem, zUniquesCollectionItemsQuery } from '../../schemas/palletUniques/item';
import {
  zUniquesAttribute,
  zUniquesClearAttributeIn,
  zUniquesClearCollectionMetadataIn,
  zUniquesGetCollectionAttributesIn,
  zUniquesSetAttributeIn,
  zUniquesSetCollectionMetadataIn } from
'../../schemas/palletUniques/attributes';
import {
  zUniquesCollectionDetails,
  zUniquesCollectionQuery,
  zUniquesCreateCollectionQuery,
  zUniquesTransferCollectionQuery } from
'../../schemas/palletUniques/collection';
import { z } from 'zod';
import { ExtrinsicEndpointsConfig, QueryEndpointConfig } from '../_types';
import {
  findFreeCollectionId,
  queryAttributes,
  queryCollectionItems,
  queryCollectionDetails,
  queryCollectionsPaginated } from
'./_queries';
import { zAddressString } from 'validation-schemas';
import { zPagination } from '../../schemas/common';


const getFreeCollectionId = new QueryEndpointConfig({
  route: '/pallet-uniques/collection/free-id',


  schemaIn: z.object({}),
  schemaOut: z.object({
    freeCollectionId: z.number()
  })





});

const createCollection = new ExtrinsicEndpointsConfig({
  route: '/pallet-uniques/collection/create',



  schemaIn: zUniquesCreateCollectionQuery,
  schemaOut: zUniquesCollectionDetails.extend({
    creator: zAddressString
  })




















});

const destroyCollection = new ExtrinsicEndpointsConfig({
  route: '/pallet-uniques/collection/destroy',



  schemaIn: zUniquesCollectionQuery.extend({
    witness: z.
    object({
      itemMetadatas: z.number().default(0),
      itemConfigs: z.number().default(0),
      attributes: z.number().default(0)
    }).
    default({})
  }),
  schemaOut: zUniquesCollectionQuery











});

const freezeCollection = new ExtrinsicEndpointsConfig({
  route: '/pallet-uniques/collection/freeze',



  schemaIn: zUniquesCollectionQuery,
  schemaOut: zUniquesCollectionDetails









});

const thawCollection = new ExtrinsicEndpointsConfig({
  route: '/pallet-uniques/collection/thaw',



  schemaIn: zUniquesCollectionQuery,
  schemaOut: zUniquesCollectionDetails









});

const setCollectionAttribute = new ExtrinsicEndpointsConfig({
  route: '/pallet-uniques/collection/set-attribute',






  schemaIn: zUniquesSetAttributeIn.omit({ itemId: true }),
  schemaOut: zUniquesCollectionDetails









});

const clearCollectionAttribute = new ExtrinsicEndpointsConfig({
  route: '/pallet-uniques/collection/clear-attribute',






  schemaIn: zUniquesClearAttributeIn.omit({ itemId: true }),
  schemaOut: zUniquesCollectionDetails









});

const setCollectionMetadata = new ExtrinsicEndpointsConfig({
  route: '/pallet-uniques/collection/set-metadata',






  schemaIn: zUniquesSetCollectionMetadataIn,
  schemaOut: zUniquesCollectionDetails









});

const clearCollectionMetadata = new ExtrinsicEndpointsConfig({
  route: '/pallet-uniques/collection/clear-metadata',






  schemaIn: zUniquesClearCollectionMetadataIn,
  schemaOut: zUniquesCollectionDetails









});

const transferCollection = new ExtrinsicEndpointsConfig({
  route: '/pallet-uniques/collection/transfer',



  schemaIn: zUniquesTransferCollectionQuery,
  schemaOut: zUniquesCollectionDetails









});

const getCollection = new QueryEndpointConfig({
  route: '/pallet-uniques/collection/get',


  schemaIn: zUniquesCollectionQuery,
  schemaOut: zUniquesCollectionDetails



});

const getCollectionsPaginated = new QueryEndpointConfig({
  route: '/pallet-uniques/collections',


  schemaIn: zPagination,
  schemaOut: z.object({
    collections: z.array(zUniquesCollectionDetails),
    isLastPage: z.boolean()
  })



});

const getCollectionAttributes = new QueryEndpointConfig({
  route: '/pallet-uniques/collection/attributes',


  schemaIn: zUniquesGetCollectionAttributesIn,
  schemaOut: z.object({
    collectionId: z.number(),
    attributes: z.array(zUniquesAttribute)
  })




});

const getCollectionItems = new QueryEndpointConfig({
  route: '/pallet-uniques/collection/items',


  schemaIn: zUniquesCollectionItemsQuery,
  schemaOut: z.object({
    collectionId: z.number(),
    items: z.array(
      z.object({
        itemId: z.number(),
        details: zUniquesItem
      })
    )
  })








});

export const PalletUniquesCollectionRoutesConfig = {
  extrinsics: {
    createCollection,
    setCollectionAttribute,
    clearCollectionAttribute,
    setCollectionMetadata,
    clearCollectionMetadata,
    transferCollection,
    destroyCollection,
    freezeCollection,
    thawCollection
  },
  queries: {
    getFreeCollectionId,
    getCollection,
    getCollectionsPaginated,
    getCollectionAttributes,
    getCollectionItems
  }
};