import { z } from 'zod';
import { QueryEndpointConfig, SectionRoutesConfig } from '../_types';
import { queryLittleEndian } from './_queries';

const getPalletVersion = new QueryEndpointConfig({
  route: '/pallet-nfts/common/pallet-version',


  schemaIn: z.object({}),
  schemaOut: z.object({
    version: z.number().openapi({ description: 'The version of the NFTs pallet' })
  })





});

const getNextCollectionId = new QueryEndpointConfig({
  route: '/pallet-nfts/common/next-collection-id',


  schemaIn: z.object({}),
  schemaOut: z.object({
    nextCollectionId: z.number().openapi({ description: 'The next collection id that is going to be used' })
  })





});

export const PalletNftsCommonRoutesConfig = {
  queries: {
    getNextCollectionId,
    getPalletVersion
  },
  extrinsics: {}
} satisfies SectionRoutesConfig;