import { QueryEndpointConfig, SectionRoutesConfig } from '../_types';
import { queryAsset, queryAssetsPaginated, queryAccountAssetBalance } from './_queries';
import { zAsset, zAssetQuery } from '../../schemas/palletAssets';
import { zPagination } from '../../schemas/common';
import { z } from 'zod';

const getAsset = new QueryEndpointConfig({
  route: '/pallet-assets/asset',


  schemaIn: zAssetQuery,
  schemaOut: zAsset






});

const getAssetsPaginated = new QueryEndpointConfig({
  route: '/pallet-assets/assets',


  schemaIn: zPagination,
  schemaOut: z.object({
    assets: z.array(zAsset),
    isLastPage: z.boolean()
  })





});

const getBalance = new QueryEndpointConfig({
  route: '/pallet-assets/balance',


  schemaIn: z.object({
    address: z.string(),
    id: z.coerce.number().int().positive().openapi({ description: 'Collection ID', example: 1 })
  }),
  schemaOut: z.object({
    balance: z.string(),
    symbol: z.string(),
    decimals: z.number()
  })






});

export const PalletAssetRoutesConfig = {
  queries: {
    getAsset,
    getAssetsPaginated,
    getBalance
  },
  extrinsics: {}
} satisfies SectionRoutesConfig;