const zEvmCallBodyBase = z.object({
  functionName: z.string().openapi({ description: 'Name of the function to call' }),
  functionArgs: z.array(z.any()).openapi({ description: 'Arguments of the function' }),
  senderAddress: z.string().optional().openapi({ description: 'Address of the sender' }),
  gasPrice: z.coerce.bigint().optional().openapi({ description: 'Gas price for the transaction', type: 'string' }),
  gasLimit: z.coerce.bigint().optional().openapi({ description: 'Gas limit for the transaction', type: 'string' }),
  value: z.coerce.bigint().optional().openapi({ description: 'The amount of the transfer in wei', type: 'string' }),
  blockNumber: z.coerce.bigint().optional().openapi({
    description: 'Block number to execute the transaction',
    type: 'string'
  })
});

const zEvmCallBody = zEvmCallBodyBase.extend({
  contract: z.object({
    address: z.string().openapi({ description: 'Address of the contract' }),
    abi: zAbi
  })
});

const zEvmCallBodyKnownContract = zEvmCallBody.extend({
  contract: z.object({
    address: z.string().openapi({ description: 'Address of the contract' })
  })
});

const unknownContractCall = new QueryEndpointConfig({

  route: `${endpointPrefix}/contract/call`,
  schemaIn: zEvmCallBody,
  schemaOut: z.any(),
  method: 'POST' as QueryHttpMethod


































});

export const collectionHelpersCallRoute = new QueryEndpointConfig({

  route: `${endpointPrefix}/collection-helpers/call`,
  method: unknownContractCall.method,
  schemaIn: zEvmCallBodyBase,
  schemaOut: unknownContractCall.schemaOut



});

export const contractHelpersCallRoute = new QueryEndpointConfig({

  route: `${endpointPrefix}/contract-helpers/call`,
  method: unknownContractCall.method,
  schemaIn: zEvmCallBodyBase,
  schemaOut: unknownContractCall.schemaOut



});

