import {z} from 'zod'
import {zUniversalLocationObject} from 'validation-schemas/xcmSchemas'

const zAssetsAddress = z.string().openapi({description: 'The owner of the collection', example: '5GEuzsNk8HkjSDJi1EK91omheS476JAW2PhqrZJSiBrEVeLL'})

export const zAsset = z.object({
  id: z.number().openapi({description: 'Asset ID', example: 1}),
  owner: zAssetsAddress,
  issuer: zAssetsAddress,
  admin: zAssetsAddress,
  freezer: zAssetsAddress,
  supply: z.string().openapi({description: 'Supply', example: '1000000000000000000000'}),
  deposit: z.string().openapi({description: 'Deposit', example: '1000000000000000000000'}),
  minBalance: z.string().openapi({description: 'Min balance', example: '1000000000000000000000'}),
  isSufficient: z.boolean().openapi({description: 'Is sufficient', example: true}),
  accounts: z.number().openapi({description: 'Accounts', example: 23}),
  sufficients: z.number().openapi({description: 'Sufficients', example: 23}),
  approvals: z.number().openapi({description: 'Approvals', example: 23}),
  status: z.string().openapi({description: 'Status', example: 'Live'}),
  name: z.string().openapi({description: 'Name', example: 'USD Stablecoin'}),
  symbol: z.string().openapi({description: 'Symbol', example: 'USD'}),
  decimals: z.number().openapi({description: 'Decimals', example: 18}),
  isFrozen: z.boolean().openapi({description: 'Is frozen', example: false}),
  universalLocation: zUniversalLocationObject.nullable().openapi({description: 'The universal location of the collection'}),
})

export type Asset = z.infer<typeof zAsset>

export const zAssetQuery =   z.object({
  id: z.coerce.number().int().positive().openapi({description: 'Collection ID', example: 1}),
})

export type AssetQuery = z.infer<typeof zAssetQuery>
