import { ExtrinsicEndpointsConfig, QueryEndpointConfig } from './_types';
import {
  zMintMultipleRefungibleTokens,
  zMintRefungibleToken,
  zRefungibleAccountBalance,
  zRefungibleAccountBalanceQuery,
  zRefungibleToken,
  zRefungibleTokenQuery } from
'../schemas/refungible';
import { Methods } from '../methods';
import { Address } from '@unique-nft/utils/address';
import { TokenEvents } from '../schemas/events';
import { z } from 'zod';
import { encodeTokenProperties } from '../utils/token';
import { zAddressString } from 'validation-schemas';

const tags = ['Refungible'];
const endpointPrefix = '/refungible';

const getRefungibleToken = new QueryEndpointConfig({

  route: `${endpointPrefix}`,
  method: 'GET',
  schemaIn: zRefungibleTokenQuery,
  schemaOut: zRefungibleToken







});

const getRefungibleAccountBalance = new QueryEndpointConfig({

  route: `${endpointPrefix}/account-balance`,
  method: 'GET',
  schemaIn: zRefungibleAccountBalanceQuery,
  schemaOut: zRefungibleAccountBalance







});

const mintRefungibleToken = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/mint`,
  schemaIn: zMintRefungibleToken,










  schemaOut: zRefungibleToken





























});

const mintRefungibleTokens = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/mint-multiple`,
  schemaIn: zMintMultipleRefungibleTokens,
  schemaOut: z.array(zRefungibleToken)






























});

export const RefungibleRoutesConfig = {
  queries: {
    getRefungibleToken,
    getRefungibleAccountBalance
  },
  extrinsics: {
    mintRefungibleToken,
    mintRefungibleTokens
  }
};