import { z } from 'zod';
import 'zod-openapi/extend';
import { coinToWei } from 'coin-format';
import { Address } from '@unique-nft/utils/address';
import * as ReusableQueries from 'reusable-queries';
import { zAddressString } from 'validation-schemas';
import { ExtrinsicEndpointsConfig, QueryEndpointConfig, SectionRoutesConfig } from './_types';
import { BalancesEvents } from '../schemas/events';
import { zAtBlockHash, zVestedItem } from '../schemas/common';

const transferBalances = new ExtrinsicEndpointsConfig({
  route: '/balances/transfer',



  schemaIn: z.object({
    to: zAddressString,
    amount: z.coerce.string().openapi({ description: 'The amount of the transfer in coins or wei (default)', example: '150000' }),
    isAmountInCoins: z.boolean().optional().openapi({
      description: 'If true, the value is in coins, otherwise in wei. Default is false',
      example: false
    })
  }),








  schemaOut: BalancesEvents.Transfer.schema

});

const getBalances = new QueryEndpointConfig({
  route: '/balances/get',


  schemaIn: z.
  object({
    address: zAddressString
  }).
  merge(zAtBlockHash),

  schemaOut: z.object({
    available: z.string().openapi({
      description: 'The amount of the available balance in coins',
      example: '1.5'
    }),
    locked: z.string().openapi({ description: 'The amount of the locked balance in wei', example: '1000000000' }),
    free: z.string().openapi({ description: 'The amount of the free balance in wei', example: '1000000000' }),
    total: z.string().openapi({ description: 'The amount of the total balance in wei', example: '1000000000' }),
    reserved: z.string().openapi({ description: 'The amount of the reserved balance in wei', example: '1000000000' }),
    staked: z.string().openapi({ description: 'The amount of the staked balance in wei', example: '1000000000' }),
    unstaked: z.string().openapi({ description: 'The amount of the unstaked balance in wei', example: '1000000000' }),
    canstake: z.string().openapi({ description: 'The amount of the canstake balance in wei', example: '1000000000' }),
    vested: z.
    array(zVestedItem).
    optional().
    openapi({
      description: 'The list of the vested balances',
      example: [{ start: '123456', period: '1', periodCount: '1', perPeriod: '1000000000' }]
    }),
    decimals: z.number().openapi({ description: 'The number of decimals of the token', example: 18 }),
    tokenSymbol: z.string().openapi({ description: 'The token symbol', example: 'UNQ' })
  })



























});

export const BalancesRoutesConfig = {
  extrinsics: {
    transferBalances
  },
  queries: {
    getBalances
  }
} satisfies SectionRoutesConfig;