import {z} from 'zod'
import {zAttribute} from './attributes'

const zNftsItemPrice = z.object({
  amount: z.string(),
  buyer: z.string().nullable(),
})

export type NftsItemPrice = z.infer<typeof zNftsItemPrice>

const zNftsItemSettings = z.object({
  lockedTransfers: z.boolean(),
  lockedMetadata: z.boolean(),
  lockedAttributes: z.boolean(),
})

export type NftsItemSettings = z.infer<typeof zNftsItemSettings>

const zNftsItemMetadata = z.object({
  data: z.string(),
  deposit: z.object({
    account: z.string().nullable(),
    amount: z.string(),
  }),
})

export type NftsItemMetadata = z.infer<typeof zNftsItemMetadata>

const zNftsTransferApproval = z.object({
  delegate: z.string(),
  deadlineBlock: z.number().optional(),
})

export type NftsTransferApproval = z.infer<typeof zNftsTransferApproval>

const zNftsItemDeposit = z.object({
  account: z.string(),
  amount: z.string(),
})

export type NftsItemDeposit = z.infer<typeof zNftsItemDeposit>

export const zNftsItem = z.object({
  collectionId: z.number(),
  itemId: z.number(),
  owner: z.string(),
  deposit: zNftsItemDeposit,
  transferApprovals: z.array(zNftsTransferApproval),
  metadata: zNftsItemMetadata.nullable(),
  price: zNftsItemPrice.nullable(),
  attributes: z.array(zAttribute),
  settings: zNftsItemSettings.nullable(),
})

export type NftsItem = z.infer<typeof zNftsItem>
