import {z} from 'zod'

export const zUniquesGetCollectionAttributesIn = z.object({
  collectionId: z.coerce.number().openapi({description: 'The ID of the collection.'}),
})

export const zUniquesItemAttributesIn = z.object({
  collectionId: z.coerce.number().openapi({description: 'The ID of the collection.'}),
  itemId: z.coerce.number().openapi({description: 'The ID of the asset.'}),
})

const zAttributesIn = z.object({
  collectionId: z.coerce.number().openapi({description: 'The ID of the collection.'}),
  itemId: z.coerce.number().optional().openapi({description: 'The ID of the item.'}),
})

export type GetUniquesAttributesQuery = z.output<typeof zAttributesIn>

export const zUniquesAttribute = z.object({
  key: z.string().openapi({description: 'Attribute key'}),
  value: z.string().nullable().openapi({description: 'Attribute value'}),
})

export const zUniquesGetAttribute = z.object({
  collectionId: z.coerce.number().openapi({description: 'The ID of the collection.'}),
  itemId: z.coerce.number().optional().openapi({description: 'The ID of the item.'}),
  attributeKey: z.string().openapi({description: 'The key of the attribute.'}),
})

export type GetUniquesAttributeQuery = z.output<typeof zUniquesGetAttribute>

export type UniquesAttribute = z.infer<typeof zUniquesAttribute>

export const zUniquesSetAttributeIn = z.object({
  collectionId: z.coerce.number().openapi({description: 'The ID of the collection.'}),
  itemId: z.coerce.number().optional().openapi({description: 'The ID of the item.'}),
  attribute: z.object({
    key: z.string().openapi({description: 'The key of the attribute.'}),
    value: z.string().openapi({description: 'The value of the attribute.'}),
  }),
})

export const zUniquesClearAttributeIn = z.object({
  collectionId: z.coerce.number().openapi({description: 'The ID of the collection.'}),
  itemId: z.coerce.number().optional().openapi({description: 'The ID of the item.'}),
  attributeKey: z.string().openapi({description: 'The key of the attribute.'}),
})

export const zUniquesSetCollectionMetadataIn = z.object({
  collectionId: z.coerce.number().openapi({description: 'The ID of the collection.'}),
  data: z.string().openapi({description: 'The metadata of the collection.'}),
  isFrozen: z.boolean().optional().default(false).openapi({description: 'Whether the collection is frozen.'}),
})

export const zUniquesMetadata = z.object({
  data: z.string().openapi({description: 'The metadata of the collection.'}),
  isFrozen: z.boolean().optional().default(false).openapi({description: 'Whether the collection is frozen.'}),
  deposit: z.string().openapi({description: 'The deposit of the collection.'}),
})

export type Metadata = z.output<typeof zUniquesMetadata>

export const zUniquesSetItemMetadataIn = zUniquesSetCollectionMetadataIn.extend({
  itemId: z.coerce.number().openapi({description: 'The ID of the item.'}),
})

export const zUniquesClearCollectionMetadataIn = z.object({
  collectionId: z.coerce.number().openapi({description: 'The ID of the collection.'}),
})

export const zUniquesClearItemMetadataIn = zUniquesClearCollectionMetadataIn.extend({
  itemId: z.coerce.number().openapi({description: 'The ID of the item.'}),
})
