import {z} from 'zod'
import {zAddressString} from 'validation-schemas'
import {zCollectionSchema} from '@unique-nft/schemas/pure'
import 'zod-openapi/extend'
import {Address} from '@unique-nft/utils/address'
import {zAtBlockHash} from './common'
import {EndpointSchema} from './types'
import {zMaxBytesPipe} from './input'
import {zUniversalLocationObject} from 'validation-schemas/xcmSchemas'

export const zCollectionIdNumber = z
  .number()
  .int()
  .min(1)
  .max(2 ** 32 - 1)
  .positive()
  .openapi({
    description: 'Collection ID',
    example: 1,
  })
export const zCollectionAddress = z
  .string()
  .regex(/^0x[0-9a-fA-F]{40}$/)
  .openapi({
    description: 'Collection address',
    example: '0x71C7656EC7ab88b098defB751B7401B5f6d8976F',
  })
export const zCollectionIdString = z
  .string()
  .regex(/^[1-9]\d*$/)
  .openapi({
    description: 'Collection ID',
    example: '1',
  })

export const zCollectionIdOrAddress = z
  .union([zCollectionIdNumber, zCollectionAddress, zCollectionIdString])
  .transform((value): number => {
    if (typeof value === 'number') {
      Address.validate.collectionId(value)
      return value
    }

    if (Address.is.collectionAddress(value)) {
      return Address.collection.addressToId(value)
    }

    const numberValue = parseInt(value, 10)
    Address.validate.collectionId(numberValue)

    return numberValue
  })
  .openapi({
    description: 'Collection ID (number) or address (in format 0x)',
  })

export const zCollectionIdQuery = z
  .object({
    collectionId: zCollectionIdOrAddress,
  })
  .merge(zAtBlockHash)

export const zCollectionLimits = z.object({
  accountTokenOwnershipLimit: z.number().nullable().optional().openapi({
    description: 'Maximum number of tokens that one address can own',
    example: 1000,
  }),
  sponsoredDataSize: z.number().nullable().optional().openapi({
    description: 'Maximum byte size of custom token data that can be sponsored when tokens are minted in sponsored mode',
    example: 1024,
  }),
  sponsoredDataRateLimit: z.union([z.number(), z.string()]).nullable().optional().openapi({
    description: 'Defines how many blocks need to pass between setVariableMetadata transactions in order for them to be sponsored',
    example: 30,
  }),
  tokenLimit: z.number().nullable().optional().openapi({
    description: 'Total amount of tokens that can be minted in this collection',
    example: 1000000,
  }),
  sponsorTransferTimeout: z.number().nullable().optional().openapi({
    description: 'Time interval in blocks that defines once per how long a non-privileged user transfer or mint transaction can be sponsored',
    example: 6,
  }),
  sponsorApproveTimeout: z.number().nullable().optional().openapi({
    description: 'Time interval in blocks that defines once per how long a non-privileged user approve transaction can be sponsored',
    example: 6,
  }),
  ownerCanTransfer: z.boolean().nullable().optional().openapi({
    description: 'Boolean value that tells if collection owner or admins can transfer or burn tokens owned by other non-privileged users',
    example: false,
  }),
  ownerCanDestroy: z.boolean().nullable().optional().openapi({
    description: 'Boolean value that tells if collection owner can destroy it',
    example: false,
  }),
  transfersEnabled: z.boolean().nullable().optional().openapi({
    description: 'Flag that defines whether token transfers between users are currently enabled',
    example: false,
  }),
})

export type CollectionLimits = z.infer<typeof zCollectionLimits>

export const zAllCollectionStats = z.object({
  created: z.number().openapi({description: 'Total number of collections created', example: 100}),
  destroyed: z.number().openapi({description: 'Total number of collections destroyed', example: 10}),
  alive: z.number().openapi({
    description: 'Total number of collections that are created and not destroyed',
    example: 90,
  }),
})

export const zCollectionDecodedSponsorship = z.object({
  isEnabled: z.boolean().openapi({
    description: 'Flag that defines whether sponsorship is enabled for this collection',
    example: true,
  }),
  isConfirmed: z.boolean().openapi({
    description: 'Flag that defines whether sponsorship is confirmed for this collection',
    example: true,
  }),
  sponsor: z.string().nullable().openapi({description: 'Address of the sponsor'}),
})

export type CollectionDecodedSponsorship = z.infer<typeof zCollectionDecodedSponsorship>

export const zDecodedProperty = z.object({
  key: z.string().openapi({description: 'The key of the property', example: 'my_key'}),
  value: z.string().openapi({description: 'The value of the property', example: 'my_value'}),
  valueHex: z.string().openapi({description: 'The value of the property in hex', example: '0x02'}),
})

export const zPropertiesToEncode = z.array(
  z.union([
    z.object({
      key: z.string().pipe(zMaxBytesPipe(64)),
      value: z.string().pipe(zMaxBytesPipe(32768)),
      valueHex: z.string().pipe(zMaxBytesPipe(32768)).optional(),
    }),
    z.object({
      key: z.string().pipe(zMaxBytesPipe(64)),
      value: z.string().pipe(zMaxBytesPipe(32768)).optional(),
      valueHex: z.string().pipe(zMaxBytesPipe(32768)),
    }),
  ]),
) /*.transform((value): Array<{key: string, valueHex: string}> => {
  return value.map((item) => {
    const value = 'value' in item ? item.value : null
    const valueHex = 'valueHex' in item
      ? item.valueHex
      : typeof value === 'string'
        ? StringUtils.Utf8.stringToHexString(value)
        : null

    if (typeof valueHex !== 'string') {
      throw new Error('value or valueHex is required')
    }

    return {
      key: item.key,
      valueHex
    }
  })
})*/

export type PropertiesToEncode = z.infer<typeof zPropertiesToEncode>

export type DecodedProperty = z.infer<typeof zDecodedProperty>

export const zPropertyPermission = z.object({
  mutable: z.boolean().openapi({description: 'Flag that defines whether the property is mutable', example: true}),
  collectionAdmin: z.boolean().openapi({
    description: 'Flag that defines whether the property can be changed by collection admin',
    example: true,
  }),
  tokenOwner: z.boolean().openapi({
    description: 'Flag that defines whether the property can be changed by token owner',
    example: true,
  }),
})

export const zTokenPropertyPermission = z.object({
  key: z.string().pipe(zMaxBytesPipe(256)).openapi({description: 'The key of the property', example: 'my_key'}),
  permission: zPropertyPermission,
})
export type TokenPropertyPermission = z.infer<typeof zTokenPropertyPermission>

export const zCollection = z.object({
  collectionId: zCollectionIdNumber,
  collectionAddress: zCollectionAddress,
  owner: z.string().openapi({description: 'The owner of the collection', example: '5GEuzsNk8HkjSDJi1EK91omheS476JAW2PhqrZJSiBrEVeLL'}),
  mode: z.enum(['NFT', 'Fungible', 'ReFungible']),
  decimals: z.number().nullable().openapi({description: 'The decimals if collection is fungible, null otherwise'}),
  name: z.string().openapi({description: 'The name of the collection', example: 'My Collection'}),
  description: z.string().openapi({description: 'The description of the collection', example: 'My Collection description'}),
  symbol: z.string().openapi({description: 'The symbol (token prefix) of the collection', example: 'MC'}),
  properties: z.array(zDecodedProperty),
  limits: zCollectionLimits.or(z.null()).openapi({description: 'The collection limits'}),
  admins: z.array(zAddressString).or(z.null()).openapi({description: 'The collection admins'}),
  lastTokenId: z.number().or(z.null()).openapi({description: 'The last token ID'}),
  totalSupply: z.number().or(z.null()).openapi({description: 'The total supply (number of minted tokens)'}),
  sponsorship: zCollectionDecodedSponsorship,
  readOnly: z.boolean().openapi({description: 'Flag that defines whether the collection is read only', example: false}),
  tokenPropertyPermissions: z.array(zTokenPropertyPermission),
  permissions: z.object({
    access: z.union([z.literal('Normal'), z.literal('AllowList')]).openapi({description: 'The access mode'}),
    mintMode: z.boolean().openapi({description: 'Flag that defines whether the mint mode is enabled'}),
    nesting: z
      .object({
        tokenOwner: z.boolean().openapi({description: 'Flag that defines whether the token owner can nest tokens'}),
        collectionAdmin: z.boolean().openapi({description: 'Flag that defines whether the collection admin can nest tokens'}),
        restricted: z.array(z.number()).optional().openapi({description: 'The restricted collection IDs'}),
      })
      .openapi({description: 'The nesting permissions'}),
  }),
  info: z.object(zCollectionSchema.shape).nullable().openapi({description: 'Collection info'}),
  infoDecodingError: z.string().nullable().openapi({description: 'Error message if decoding of collection info failed'}),
  universalLocation: zUniversalLocationObject.nullable().openapi({description: 'The universal location of the collection'}),
  originalUniversalLocation: zUniversalLocationObject.nullable().openapi({description: 'The universal location of the collection'}),
  isForeign: z.boolean().nullable().openapi({description: 'Flag that defines whether the collection is foreign'}),
  isErc721Metadata: z.boolean().nullable().openapi({description: 'Flag that defines whether the collection is ERC721 metadata compatible'}),
})

export type Collection = z.infer<typeof zCollection>

export const getAllCollectionsStatsSchema = {
  route: `/allCollectionsStats`,
  schemaIn: zAtBlockHash,
  schemaOut: zAllCollectionStats,
} satisfies EndpointSchema

export const zCollectionAccess = z.enum(['Normal', 'AllowList']).openapi({description: 'The access mode', example: 'Normal'})

export const zCreateCollection = z.object({
  mode: z.enum(['Nft', 'Fungible', 'ReFungible']).openapi({description: 'The mode of the collection', example: 'Nft'}),
  tokenDecimals: z.coerce.number().int().positive().optional().openapi({description: 'Fungible token decimals', example: 18}),
  name: z.string().pipe(zMaxBytesPipe(64)).openapi({description: 'The name of the collection', example: 'My Collection'}),
  description: z.string().pipe(zMaxBytesPipe(256)).openapi({
    description: 'The description of the collection',
    example: 'My Collection description',
  }),
  symbol: z.string().optional().pipe(zMaxBytesPipe(16)).openapi({description: 'The symbol of the collection', example: 'MC'}),
  access: zCollectionAccess.optional(),
  limits: zCollectionLimits.partial().optional().openapi({description: 'The limits of the collection'}),
  admins: z.array(zAddressString).openapi({description: 'The collection admins'}).optional(),
  permissions: z
    .object({
      access: zCollectionAccess.optional(),
      nesting: z
        .object({
          tokenOwner: z.boolean().optional().openapi({
            description: 'Flag that defines whether the token owner can be changed',
            example: true,
          }),
          collectionAdmin: z.boolean().optional().openapi({
            description: 'Flag that defines whether the collection admin can be changed',
            example: true,
          }),
          restricted: zCollectionIdOrAddress.array().optional().openapi({description: 'The restricted collection IDs or addresses'}),
        })
        .optional()
        .openapi({description: 'The nesting permissions'}),
      mintMode: z.boolean().optional().openapi({
        description: 'Flag that defines whether the mint mode is enabled',
        example: true,
      }),
    })
    .optional(),
  pendingSponsor: zAddressString
    .optional()
    .openapi({description: 'The pending sponsor'})
    .transform((value) => {
      return typeof value === 'string' ? Address.extract.crossAccountIdNormalized(value) : undefined
    }),

  info: zCollectionSchema.optional().openapi({description: 'Collection info'}),
  properties: zPropertiesToEncode.optional().openapi({description: 'The properties of the collection'}),
  tokenPropertyPermissions: z.array(zTokenPropertyPermission).optional().openapi({description: 'The token property permissions (collection setting)'}),
})
