import { ExtrinsicEndpointsConfig, SectionRoutesConfig } from '../_types';
import { EventNotFoundInExtrinsicError } from '../../schemas/errors';
import { z } from 'zod';
import { zAddressString } from 'validation-schemas';
import { Address } from '@unique-nft/utils/address';

const setPrice = new ExtrinsicEndpointsConfig({
  route: '/pallet-nfts/items/set-price',















  schemaIn: z.object({
    collectionId: z.number().positive(),
    itemId: z.number().positive(),
    price: z.union([z.number().positive(), z.string()]).nullable().optional(),
    buyer: zAddressString.optional()
  }),
  schemaOut: z.any()



















});

const buyItem = new ExtrinsicEndpointsConfig({
  route: '/pallet-nfts/items/buy',













  schemaIn: z.object({
    collectionId: z.number().positive(),
    itemId: z.number().positive(),
    bidPrice: z.union([z.number().positive(), z.string().regex(/^\d+$/)])
  }),
  schemaOut: z.object({
    collectionId: z.number(),
    itemId: z.number(),
    price: z.string(),
    seller: z.string(),
    buyer: z.string()
  })



















});

export const PalletNftsTradesRoutesConfig = {
  queries: {},
  extrinsics: {
    setPrice,
    buyItem
  }
} satisfies SectionRoutesConfig;