import {z} from 'zod'

const uint8ArraySchema = z.string().regex(/^0x[a-fA-F0-9]+$/)

const zNetworkIdLiteral = z.enum(['polkadot', 'kusama', 'bitcoinCore', 'bitcoinCash', 'polkadotBulletin'] as const)

const zNetworkIdByGenesis = z.object({
  byGenesis: uint8ArraySchema,
})

export const zNetworkId = z.union([zNetworkIdByGenesis, zNetworkIdLiteral])

const zUniversalLocationObject = z.object({
  globalConsensus: zNetworkId,
  parachain: z.number().optional(),
  palletInstance: z.number().optional(),
  generalIndex: z.number().optional(),
})

const zTransferParams = z.object({
  senderAddress: z.string(),
  recipientAddress: z.string().optional(),
  itemId: z.number(),
  collectionUniversalLocation: zUniversalLocationObject,
  sourceParachainId: z.number(),
  destinationParachainId: z.number(),
})

const zFeeAsset = z.object({
  universalLocation: zUniversalLocationObject,
  decimals: z.number(),
})

export const zTransferNftParams = zTransferParams.extend({
  itemId: z.number(),
  feeAsset: zFeeAsset,
})

export const zTransferFungibleParams = zTransferParams.extend({
  amount: z.number(),
  decimals: z.number(),
  feeAsset: zFeeAsset.optional(),
})

const zXcmTransferResult = z.object({
  xcmMessageId: z.string(),
  xcmMessageHash: z.string(),
})

export const xcmMethods = {
  transferNft: {
    route: '/xcm/nft-transfer',
    schemaIn: zTransferNftParams,
    encodeSchemaIn: zTransferNftParams,
    schemaOut: zXcmTransferResult,
  },
  transferFungible: {
    route: '/xcm/fungible-transfer',
    schemaIn: zTransferFungibleParams,
    encodeSchemaIn: zTransferNftParams,
    schemaOut: zXcmTransferResult,
  },
  feeAssets: {
    route: '/xcm/fee-assets',
    schemaIn: z.object({
      parachainId: z.number(),
    }),
    encodeSchemaIn: z.object({
      parachainId: z.number(),
    }),
    schemaOut: z.array(zUniversalLocationObject),
  },
}
