import {z} from 'zod'
import 'zod-openapi/extend'

import {zSignerPayloadHex, zSignerPayloadJSON, zSignerPayloadRaw, zSubstrateAddress} from './shared'

// todo - duplicate with schema from substrate-http-proxy/src/schemas/common.ts
export const zQueryBoolean = z
  .union([z.string(), z.boolean(), z.number()])
  .transform((val) => {
    if (typeof val === 'string') return val.toLowerCase() === 'true'
    if (typeof val === 'boolean') return val

    return val !== 0
  })
  .openapi({description: 'Boolean query parameter', type: 'boolean'})

const zArgsArray = z.array(z.any()).openapi({
  description: 'The arguments to pass to the method',
  example: ['5GEuzsNk8HkjSDJi1EK91omheS476JAW2PhqrZJSiBrEVeLL', 777],
})

const zArgsObject = z
  .object({})
  .catchall(z.any())
  .openapi({
    description: 'The arguments to pass to the method',
    example: {
      to: '5GEuzsNk8HkjSDJi1EK91omheS476JAW2PhqrZJSiBrEVeLL',
      value: 777,
    },
  })

export const zBuildExtrinsic = z.object({
  signerAddress: zSubstrateAddress,
  section: z.string().openapi({
    description: 'The section of the method to call',
    example: 'balances',
  }),
  method: z.string().openapi({
    description: 'The method to call',
    example: 'transferKeepAlive',
  }),
  args: z.union([zArgsArray, zArgsObject]),
  mortalLength: z.number().optional(),
  nonce: z.number().optional(),
})

export type BuildExtrinsic = z.infer<typeof zBuildExtrinsic>

export const zEncodeExtrinsic = zBuildExtrinsic.omit({
  signerAddress: true,
  mortalLength: true,
  nonce: true,
})

export type EncodeMethodParams = Pick<BuildExtrinsic, 'section' | 'method' | 'args'>

export type EncodeExtrinsic = z.infer<typeof zEncodeExtrinsic>

export const zCompactExtrinsic = z.object({
  compactExtrinsic: z.string().openapi({
    description: 'The encoded compact extrinsic, same as call, but with version and length',
    example: '0x1e03008ea22863f6d84b1f76e4377c1ba1f6c8bd75b0d3bb5d11c36c0436bdd3110867e5c0',
  }),
})

export const zSignerPayload = z.object({
  signerPayloadJSON: zSignerPayloadJSON,
  signerPayloadRaw: zSignerPayloadRaw,
  signerPayloadHex: zSignerPayloadHex,
})

export const zSignerPayloadWithCompactExtrinsic = zSignerPayload.extend({
  compactExtrinsic: z.string().openapi({
    description: 'The encoded compact extrinsic, same as call, but with version and length',
    example: '0x1e03008ea22863f6d84b1f76e4377c1ba1f6c8bd75b0d3bb5d11c36c0436bdd3110867e5c0',
  }),
})

export type SignerPayload = z.infer<typeof zSignerPayload>

const zSubmitExtrinsic = z.object({
  signerPayloadJSON: zSignerPayloadJSON,
  signature: z.string(),
})

const zSubmittedExtrinsic = z.object({
  hash: z.string(),
})

export const buildExtrinsic = {
  input: zBuildExtrinsic,
  output: zSignerPayload,
}

export const encodeExtrinsic = {
  input: zEncodeExtrinsic,
  output: zCompactExtrinsic,
}

export const submitExtrinsic = {
  input: zSubmitExtrinsic,
  output: zSubmittedExtrinsic,
}

const zExtrinsicStatus = z
  .object({
    hash: z.string(),
    signer: zSubstrateAddress,
    section: z.string(),
    method: z.string(),
    args: z.object({}).catchall(z.any()),
    events: z.array(
      z.object({
        section: z.string(),
        method: z.string(),
        data: z.object({}).catchall(z.any()),
      }),
    ),
    groupedEvents: z.object({}).catchall(z.object({}).catchall(z.array(z.object({}).catchall(z.any())))),
    blockHash: z.string(),
    blockNumber: z.coerce.string(),
  })
  .openapi({
    description: 'The status of the extrinsic',
    example: {
      hash: '0xe888db18399714543558de271b1e67c68caad3f2778a9cb45cf03cd72a62735c',
      blockHash: '0xef7d8c3d68fb2f4e551b6d4dac534bf80f78b9137729e4442cc9d72d9798469e',
      blockNumber: '1111969',
      signer: '5GrwvaEF5zXb26Fz9rcQpDWS57CtERHpNehXCPcNoHGKutQY',
      section: 'balances',
      method: 'transferKeepAlive',
      args: {
        to: '5GrwvaEF5zXb26Fz9rcQpDWS57CtERHpNehXCPcNoHGKutQY',
        amount: '1500000000000000000',
      },
      events: [
        {
          section: 'balances',
          method: 'Withdraw',
          data: {
            who: '5GrwvaEF5zXb26Fz9rcQpDWS57CtERHpNehXCPcNoHGKutQY',
            amount: '59280745607318347',
          },
        },
        '...' as any,
      ],
      groupedEvents: {
        balances: {
          Withdraw: [
            {
              who: '5GrwvaEF5zXb26Fz9rcQpDWS57CtERHpNehXCPcNoHGKutQY',
              amount: '59280745607318347',
            },
            '...' as any,
          ],
        },
      },
    },
  })

export type ExtrinsicStatus = z.infer<typeof zExtrinsicStatus>

export const getExtrinsicStatus = {
  input: z.object({
    hash: z.string(),
    isFinalized: zQueryBoolean.optional(),
  }),
  output: {
    ok: zExtrinsicStatus,
    notFound: z.object({
      error: z.string(),
    }),
  },
}

export const zExtrinsicFee = zBuildExtrinsic

export const zExtrinsicFeeOutput = z.object({
  baseFee: z.coerce.string(),
  lenFee: z.coerce.string(),
  adjustedWeightFee: z.coerce.string(),
  totalFee: z.coerce.string(),
  xcmFee: z.coerce.string(),
})

////////////////////////////////////
// stuff
////////////////////////////////////

export const zErrors = {
  notFound: z.object({error: z.string()}),
  generic: z.object({
    error: z.string(),
    details: z.any().optional(),
    httpPart: z.string().optional(),
  }),
}

export const zExtrinsicApi = {
  buildInput: zBuildExtrinsic,
  buildOutput: zSignerPayloadWithCompactExtrinsic,
  encodeInput: zEncodeExtrinsic,
  encodeOutput: zCompactExtrinsic,
  extrinsicFeeInput: zExtrinsicFee,
  extrinsicFeeOutput: zExtrinsicFeeOutput,
  submitInput: zSubmitExtrinsic,
  submitOutput: z.object({hash: z.string()}),
  getStatusInput: z.object({hash: z.string()}), // todo - duplicate with getExtrinsicStatus.input
  getStatusOutput: zExtrinsicStatus,
}

export type IGetExtrinsicStatusInput = z.infer<typeof zExtrinsicApi.getStatusInput>
export type IGetExtrinsicStatusOutput = z.infer<typeof zExtrinsicApi.getStatusOutput>

export const zExtrinsicHash = z
  .string()
  .trim()
  .toLowerCase()
  .regex(/^0x[0-9a-f]{64}$/)
  .openapi({
    description: 'The hash of the extrinsic',
    example: '0x96affcbd6740d3d94b90996aee65b8a3075512c0e8bbedb92e79df0f5d2cad5f',
  })

export type IExtrinsicHash = z.infer<typeof zExtrinsicHash>
