import { Address } from '@unique-nft/utils/address';
import { zNftsItem } from '../../schemas/palletNfts/item';
import { zAddressString } from 'validation-schemas';
import { z } from 'zod';
import { EventNotFoundInExtrinsicError } from '../../schemas/errors';
import { zAttribute } from '../../schemas/palletNfts/attributes';
import { ExtrinsicEndpointsConfig, QueryEndpointConfig, SectionRoutesConfig } from '../_types';
import { queryItem } from './_queries';

const getItem = new QueryEndpointConfig({
  route: '/pallet-nfts/items/get',


  schemaIn: z.object({
    collectionId: z.coerce.number().positive(),
    itemId: z.coerce.number().positive()
  }),
  schemaOut: zNftsItem



});

const mintItem = new ExtrinsicEndpointsConfig({
  route: '/pallet-nfts/items/mint',



















  schemaIn: z.object({
    collectionId: z.number().nonnegative(),
    itemId: z.number().nonnegative(),
    mintTo: zAddressString,
    witnessData: z.
    object({
      ownedItemId: z.number().optional(),
      mintPrice: z.number().optional()
    }).
    optional()
  }),
  schemaOut: zNftsItem














});

const burnItem = new ExtrinsicEndpointsConfig({
  route: '/pallet-nfts/items/burn',















  schemaIn: z.object({
    collectionId: z.number().positive(),
    itemId: z.number().positive()
  }),
  schemaOut: z.any()















});

const approveItemAttributes = new ExtrinsicEndpointsConfig({
  route: '/pallet-nfts/items/approve-attributes',














  schemaIn: z.object({
    collectionId: z.number().positive(),
    itemId: z.number().positive(),
    delegate: zAddressString
  }),
  schemaOut: z.any()

















});

const approveItemTransfer = new ExtrinsicEndpointsConfig({
  route: '/pallet-nfts/items/approve-transfer',














  schemaIn: z.object({
    collectionId: z.number().positive(),
    itemId: z.number().positive(),
    delegate: zAddressString,
    deadline: z.number().positive().optional().openapi({ description: 'Transfer will be approved in next N blocks' })
  }),
  schemaOut: z.any()


















});

