const lockItemPropertiesIn = z.object({
  collectionId: z.number().positive(),
  itemId: z.number().positive(),
  lockMetadata: z.boolean().optional().openapi({ description: 'Lock metadata, default: false' }),
  lockAttributes: z.boolean().optional().openapi({ description: 'Lock attributes, default: false' })
});

const lockItemProperties = new ExtrinsicEndpointsConfig({
  route: '/pallet-nfts/items/lock-properties',















  schemaIn: lockItemPropertiesIn,
  schemaOut: lockItemPropertiesIn



















});

export const PalletNftsItemRoutesConfig = {
  queries: {
    getItem
  },
  extrinsics: {
    mintItem,
    burnItem,
    approveItemAttributes,
    approveItemTransfer,
    cancelItemTransferApproval,
    clearAllTransferApprovals,
    cancelItemAttributesApproval,
    lockItemProperties,
    setItemAttribute,
    clearItemAttribute,
    setItemMetadata,
    clearItemMetadata,
    transferItem,
    lockItemTransfer,
    unlockItemTransfer
  }
} satisfies SectionRoutesConfig;