import { z } from 'zod';
import { SubstrateClient } from 'substrate-client';
import { ExtrinsicEndpointsConfig, QueryEndpointConfig, SectionRoutesConfig } from '../_types';
import { locationFromObject } from '../../utils/xcm';
import { UniversalLocationObject, zUniversalLocationObject } from 'validation-schemas/xcmSchemas';
import { zMaxBytesPipe } from '../../schemas/input';
import { encodeCollectionMode } from '../../utils/collection';
import { StringUtils } from '@unique-nft/utils';

const queryDerivativeRegistered = async (
substrateClient: SubstrateClient,
universalLocation: UniversalLocationObject) =>
{
  const transformed = locationFromObject(universalLocation);

  const classRaw = await substrateClient.getStorage('derivativeCollections', 'originalToDerivative', [transformed]);
  const registered = !!classRaw && classRaw !== '0x';

  return { registered };
};

const getDerivativeRegistered = new QueryEndpointConfig({
  route: '/derivative-collections/registered',

  method: 'POST',
  schemaIn: z.object({
    universalLocation: zUniversalLocationObject
  }),
  schemaOut: z.object({
    registered: z.boolean()
  })



});

const registerDerivativeUnique = new ExtrinsicEndpointsConfig({
  route: '/derivative-collections/register',



  schemaIn: z.object({
    universalLocation: zUniversalLocationObject,
    mode: z.enum(['Nft', 'Fungible']).openapi({ description: 'The mode of the collection', example: 'Nft' }),
    name: z.string().pipe(zMaxBytesPipe(64)).openapi({ description: 'The name of the collection', example: 'My Collection' }),
    tokenDecimals: z.coerce.number().int().positive().optional().openapi({ description: 'Fungible token decimals', example: 18 }),
    symbol: z.string().optional().pipe(zMaxBytesPipe(16)).openapi({ description: 'The symbol of the collection', example: 'MC' })
  }),
  schemaOut: z.object({
    registered: z.boolean()
  })











});

const registerDerivativeAssetHub = new ExtrinsicEndpointsConfig({
  route: '/derivative-collections/register',



  schemaIn: z.object({
    universalLocation: zUniversalLocationObject
  }),
  schemaOut: z.object({
    registered: z.boolean()
  })









});

export const UniqueDerivativeCollectionsRoutesConfig = {
  queries: {
    getDerivativeRegistered
  },
  extrinsics: {
    registerDerivative: registerDerivativeUnique
  }
} satisfies SectionRoutesConfig;

export const AssetHubDerivativeCollectionsRoutesConfig = {
  queries: {
    getDerivativeRegistered
  },
  extrinsics: {
    registerDerivative: registerDerivativeAssetHub
  }
};