import type {ParsedExtrinsic} from 'blocks-storage/types'
import {ZodError} from 'zod'

export class ApiError extends Error {
  constructor(
    message: string,
    public readonly statusCode: number = 500,
    public readonly details?: any,
  ) {
    super(message)
  }
}

export class ValidationError extends Error {
  readonly statusCode = 401
  constructor(public readonly zodError: ZodError) {
    const errors = zodError.format()._errors
    super(`Validation Error${errors.length > 1 ? `s (${errors.length})` : ''}: ${zodError.format()._errors.join(', ')}`)
  }
}

export class NotFoundError extends ApiError {
  readonly statusCode = 404

  constructor(message: string) {
    super(message)
  }
}

export class EventNotFoundInExtrinsicError extends Error {
  readonly statusCode = 503

  constructor(extrinsic: ParsedExtrinsic, event: string) {
    super(`The extrinsic (hash ${extrinsic.hash}, block ${extrinsic.blockNumber} / ${extrinsic.blockHash}) has no ${event} event`)
  }
}

export class ExtrinsicFailedError extends Error {
  readonly statusCode = 400

  constructor(extrinsic: ParsedExtrinsic, reason: string) {
    super(`The extrinsic failed for reason ${reason}, (hash ${extrinsic.hash}, block ${extrinsic.blockNumber} / ${extrinsic.blockHash})`)
  }
}

export class NotImplementedError extends Error {
  readonly statusCode = 501

  constructor() {
    super(`Not implemented`)
  }
}
