import {zAddressString} from 'validation-schemas'
import {z} from 'zod'
import {zCollectionIdOrAddress} from './collection'

export const zMintFungibleTokens = z.object({
  collectionId: zCollectionIdOrAddress.openapi({description: 'Collection ID or address', example: 1}),
  recipient: zAddressString.optional(),
  amount: z.coerce.string().openapi({example: '150000', type: ['string', 'number', 'integer']}),
})

export const zMintFungibleTokenResult = zMintFungibleTokens.extend({
  collectionId: z.number().int().positive().openapi({description: 'Collection ID', example: 1}),
})

export const zTransferFungibleTokens = z.object({
  collectionId: zCollectionIdOrAddress.openapi({description: 'Collection ID or address', example: 1}),
  from: zAddressString.optional().openapi({description: 'Sender address (for "transfer from" operations)'}),
  to: zAddressString.openapi({description: 'Recipient address', example: '5GrwvaEF5zXb26Fz9rcQpDWS57CtERHpNehXCPcNoHGKutQY'}),
  amount: z.coerce.string().openapi({example: '150000', type: ['string', 'number', 'integer']}),
})

export const zTransferFungibleTokensResult = zTransferFungibleTokens.extend({
  collectionId: z.number().int().positive().openapi({description: 'Collection ID', example: 1}),
})

export const zBurnFungibleTokens = z.object({
  collectionId: zCollectionIdOrAddress.openapi({description: 'Collection ID or address', example: 1}),
  from: zAddressString.optional().openapi({description: 'Token owner address (for "burn from" operations)'}),
  amount: z.coerce.string().openapi({example: '150000', type: ['string', 'number', 'integer']}),
})

export const zBurnFungibleTokensResult = zBurnFungibleTokens.extend({
  collectionId: z.number().int().positive().openapi({description: 'Collection ID', example: 1}),
})

export const zApproveFungibleTokens = z.object({
  collectionId: zCollectionIdOrAddress.openapi({description: 'Collection ID or address', example: 1}),
  sender: zAddressString.optional().openapi({description: 'Sender address', example: '5GrwvaEF5zXb26Fz9rcQpDWS57CtERHpNehXCPcNoHGKutQY'}),
  spender: zAddressString.openapi({description: 'Spender address', example: '5GrwvaEF5zXb26Fz9rcQpDWS57CtERHpNehXCPcNoHGKutQY'}),
  amount: z.coerce.string().openapi({example: '150000', type: ['string', 'number', 'integer']}),
})

export const zApproveFungibleTokensResult = zApproveFungibleTokens.omit({sender: true}).extend({
  collectionId: z.number().int().positive().openapi({description: 'Collection ID', example: 1}),
})
