import {zCollectionIdOrAddress, zDecodedProperty} from './collection'
import {zAtBlockHash, zQueryBoolean} from './common'
import {z} from 'zod'
import {SchemaTools} from '@unique-nft/schemas'
import {zAddressString} from 'validation-schemas'
import {Address} from '@unique-nft/utils/address'

export const {zTokenSchema} = SchemaTools.tools.schemas

export type TokenData = z.infer<typeof zTokenSchema>

export const zApiQueryTokenId = zAtBlockHash.extend({
  collectionId: zCollectionIdOrAddress.openapi({description: 'Collection ID or address', example: 1}),
  tokenId: z.coerce.number().int().positive().openapi({description: 'Token ID', example: 1}),
  withChildren: zQueryBoolean.optional().openapi({description: 'If true, the nested tokens will be included in the result', example: false}),
})

export const zApiResultToken = zTokenSchema.partial().extend({
  tokenId: z.number().int().positive().openapi({description: 'Token ID', example: 1}),
  collectionId: z.number().int().positive().openapi({description: 'Collection ID', example: 1}),
  collectionAddress: z.string().openapi({description: 'Collection address'}),
  symbol: z.string().openapi({description: 'Symbol (token prefix)'}),
  owner: z.string().openapi({description: 'Owner address'}),
  parentToken: z
    .object({
      collectionId: z.number().int().positive().openapi({description: 'Collection ID', example: 1}),
      tokenId: z.number().int().positive().openapi({description: 'Token ID', example: 1}),
    })
    .optional()
    .openapi({description: 'Parent token (if the token is nested)'}),
  topmostOwner: z.string().openapi({description: 'Topmost owner address (if the token is nested)'}),
  properties: z.array(zDecodedProperty),
  decodingError: z.string().nullable().openapi({description: 'Error message if decoding failed'}),
})
export type Token = z.infer<typeof zApiResultToken>

export const zTokenURI = z
  .string()
  .optional()
  .openapi({description: 'The URI of the token, optional param which might be used for special extended ERC721 standards compat'})

export type TokenURI = z.infer<typeof zTokenURI>

export const zTokenPossibleActionsQuery = z.object({
  sender: zAddressString.openapi({description: 'Sender address', example: '5GEuzsNk8HkjSDJi1EK91omheS476JAW2PhqrZJSiBrEVeLL'}),
  collectionId: z.coerce.number().int().nonnegative().openapi({description: 'Collection ID or address', example: 1}),
  tokenId: z.coerce.number().int().nonnegative().openapi({description: 'Token ID', example: 1}),
  transferTo: zAddressString.optional().openapi({description: 'Recipient address, optional'}),
  nestTo: z
    .object({
      collectionId: z.coerce.number().int().nonnegative(),
      tokenId: z.coerce.number().int().nonnegative(),
    })
    .optional()
    .openapi({description: 'Token nest options, optional'}),
  amount: z.coerce.number().int().nonnegative().optional().default(1).openapi({description: 'Amount of tokens to transfer'}),
})

export type TokenPossibleActionsQuery = z.infer<typeof zTokenPossibleActionsQuery>

export const zApiResultTokenWithChildren = zApiResultToken.extend({
  children: z.array(zApiResultToken).optional().openapi({description: 'Nested tokens'}),
})

export const zTokenId = z.object({
  collectionId: zCollectionIdOrAddress.openapi({description: 'Collection ID or address', example: 1}),
  tokenId: z.number().int().positive().openapi({description: 'Token ID', example: 1}),
})

export const zNestToken = z.object({
  parent: zTokenId,
  nested: zTokenId,
  from: zAddressString.optional().openapi({description: 'Sender address (for "transfer from" operations)'}),
  amount: z.number().int().positive().optional().openapi({description: 'Amount of tokens to nest', example: 1}),
})

export const zApproveToken = z.object({
  spender: zAddressString,
  collectionId: zCollectionIdOrAddress.openapi({description: 'Collection ID or address', example: 1}),
  tokenId: z.number().int().positive().openapi({description: 'Token ID', example: 1}),
  amount: z.number().int().nonnegative().optional().openapi({description: 'Amount of tokens to approve, default is 1', example: 1}),
})

export const zUnnestToken = z.object({
  to: zAddressString.optional().openapi({description: 'Recipient address'}),
  nested: zTokenId,
  amount: z.number().int().positive().optional().openapi({description: 'Amount of tokens to transfer (for Fungible and Refungible tokens))', example: 1}),
})
