import { z } from 'zod';
import { SubstrateClient } from 'substrate-client';
import { ExtrinsicEndpointsConfig, QueryEndpointConfig, SectionRoutesConfig } from '../_types';
import { decodeLocation, locationFromObject } from '../../utils/xcm';
import { UniversalLocationObject, zUniversalLocationObject } from 'validation-schemas/xcmSchemas';
import { zPagination } from '../../schemas/common';
import { PalletUniquesCollectionDetails } from '../palletUniques/_queries';
import { zAddressString } from 'validation-schemas';

const queryDerivativeRegistered = async (
substrateClient: SubstrateClient,
universalLocation: UniversalLocationObject) =>
{
  const transformed = locationFromObject(universalLocation);

  const classRaw = await substrateClient.getStorage('foreignUniques', 'class', [transformed]);
  const registered = !!classRaw && classRaw !== '0x';

  return { registered };
};

const getDerivativeRegistered = new QueryEndpointConfig({
  route: '/pallet-foreign-uniques-asset-hub/registered',

  method: 'POST',
  schemaIn: z.object({
    universalLocation: zUniversalLocationObject
  }),
  schemaOut: z.object({
    registered: z.boolean()
  })



});

const listDerivatives = new QueryEndpointConfig({
  route: '/pallet-foreign-uniques-asset-hub/list',

  method: 'GET',
  schemaIn: zPagination,
  schemaOut: z.array(
    z.object({
      itemsCount: z.number(),
      universalLocation: zUniversalLocationObject
    })
  )


























});

const registerDerivative = new ExtrinsicEndpointsConfig({
  route: '/pallet-foreign-uniques-asset-hub/register',



  schemaIn: z.object({
    universalLocation: zUniversalLocationObject
  }),
  schemaOut: z.object({
    registered: z.boolean()
  })




});

const getAccountItems = new QueryEndpointConfig({
  route: '/pallet-foreign-uniques-asset-hub/account-items',

  method: 'POST',
  schemaIn: z.object({
    universalLocation: zUniversalLocationObject,
    account: zAddressString
  }),
  schemaOut: z.object({
    items: z.array(z.number())
  })














});

export const PalletForeignUniquesAssetHubRoutesConfig = {
  queries: {
    getDerivativeRegistered,
    listDerivatives,
    getAccountItems
  },
  extrinsics: {
    registerDerivative
  }
} satisfies SectionRoutesConfig;