// todo - move this somewhere else
export const filterBatchEvents = (parsedExtrinsic: ParsedExtrinsic, indexInBatch: number): Pick<ParsedExtrinsic, 'events' | 'groupedEvents'> => {
  const filteredEvents = parsedExtrinsic.events.reduce<{
    e: ParsedEvent[];
    currentBatch: number;
  }>(
    (acc, event) => {
      if (acc.currentBatch === indexInBatch) acc.e.push(event);

      if (event.section === 'system') {
        acc.e.push(event);
      } else if (event.section === 'utility' && event.method === 'ItemCompleted') {
        acc.currentBatch++;
      }

      return acc;
    },
    {
      e: [] as ParsedEvent[],
      currentBatch: 0
    }
  );

  const groupedEvents = groupEvents(filteredEvents.e);

  return {
    events: filteredEvents.e,
    groupedEvents
  };
};