import { Address } from '@unique-nft/utils/address';
import { SchemaTools } from '@unique-nft/schemas';
import { StringUtils } from '@unique-nft/utils';
import { zAddressString, zSubstrateAddress } from 'validation-schemas';
import { SCHEMAS_CONSTANTS } from '@unique-nft/schemas/pure';
import {
  zCollection,
  zCollectionIdOrAddress,
  zCollectionIdQuery,
  zCollectionLimits,
  zPropertiesToEncode,
  zCreateCollection,
  zTokenPropertyPermission } from
'../schemas/collection';
import { CollectionEvents } from '../schemas/events';
import { Methods } from '../methods';
import { zAllCollectionStats } from '../schemas/collection';
import { ExtrinsicEndpointsConfig, QueryEndpointConfig, SectionRoutesConfig } from './_types';
import { z } from 'zod';
import { zAtBlockHash, zPagination, zQueryBoolean } from '../schemas/common';
import { encodeCollectionLimits, encodeCollectionMode, encodeProperties } from '../utils/collection';

const tags = ['Collection'];

const endpointPrefix = '/collection';

const getCollectionById = new QueryEndpointConfig({

  route: `${endpointPrefix}`,
  schemaIn: zCollectionIdQuery.extend({
    withAdmins: zQueryBoolean.optional().default(true).openapi({
      description: 'If true, the admins will be included in the result',
      example: true
    }),
    withLimits: zQueryBoolean.optional().default(true).openapi({
      description: 'If true, the limits will be included in the result',
      example: true
    }),
    withLastTokenId: zQueryBoolean.optional().default(true).openapi({
      description: 'If true, the last token ID will be included in the result',
      example: true
    }),
    withTotalSupply: zQueryBoolean.optional().default(true).openapi({
      description: 'If true, the total supply will be included in the result',
      example: true
    })
  }),
  schemaOut: zCollection










});

const getCollectionsPaginated = new QueryEndpointConfig({

  route: `${endpointPrefix}/list`,
  schemaIn: zPagination,
  schemaOut: z.object({
    collections: z.array(zCollection),
    isLastPage: z.boolean()
  })



});

const getAllCollectionsStats = new QueryEndpointConfig({

  route: `/allCollectionsStats`,
  schemaIn: zAtBlockHash,
  schemaOut: zAllCollectionStats







});

const getAccountTokens = new QueryEndpointConfig({

  route: '/collection/account/tokens',
  schemaIn: zCollectionIdQuery.extend({
    address: zAddressString
  }),
  schemaOut: z.array(
    z.object({
      collectionId: z.number().int().positive(),
      tokenId: z.number().int().positive(),
      originalTokenId: z.number().int().positive().optional()
    }))





});

const createCollection = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/create`,




  schemaIn: zCreateCollection,



































  schemaOut: zCollection





});

const setCollectionProperties = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/properties/set`,


  schemaIn: z.object({
    collectionId: zCollectionIdOrAddress,
    properties: zPropertiesToEncode
  }),






  schemaOut: zCollection




});

const setTokenPropertyPermissions = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/token-property-permissions`,


  schemaIn: z.object({
    collectionId: zCollectionIdOrAddress,
    tokenPropertyPermissions: z.array(zTokenPropertyPermission)
  }),
  schemaOut: zCollection









});

const deleteCollectionProperties = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/properties/delete`,


  schemaIn: z.object({
    collectionId: zCollectionIdOrAddress,
    keys: z.array(z.string()).min(1)
  }),
  schemaOut: zCollection











});

const setCollectionSponsor = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/sponsor/set`,


  schemaIn: z.object({
    collectionId: zCollectionIdOrAddress,
    sponsor: zSubstrateAddress
  }),
  schemaOut: zCollection










});

const confirmCollectionSponsorship = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/sponsor/confirm`,


  schemaIn: z.object({
    collectionId: zCollectionIdOrAddress
  }),
  schemaOut: zCollection





});

const transferCollection = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/transferCollection`,


  schemaIn: z.object({
    collectionId: zCollectionIdOrAddress,
    to: zAddressString
  }),
  schemaOut: zCollection










});

const setCollectionLimits = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/limits/set`,


  schemaIn: z.object({
    collectionId: zCollectionIdOrAddress,
    limits: zCollectionLimits
  }),
  schemaOut: zCollection

















});

const addCollectionAdmin = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/admin/add`,


  schemaIn: z.object({
    collectionId: zCollectionIdOrAddress,
    newAdmin: zAddressString
  }),
  schemaOut: zCollection













});

const removeCollectionAdmin = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/admin/remove`,


  schemaIn: z.object({
    collectionId: zCollectionIdOrAddress,
    adminToDelete: zAddressString
  }),
  schemaOut: zCollection













});

const deleteCollection = new ExtrinsicEndpointsConfig({

  route: `${endpointPrefix}/delete`,


  schemaIn: z.object({
    collectionId: zCollectionIdOrAddress
  }),
  schemaOut: z.object({
    collectionId: z.number()
  })











});

export const CollectionRoutesConfig = {
  queries: {
    getCollectionById,
    getCollectionsPaginated,
    getAllCollectionsStats,
    getAccountTokens
  },
  extrinsics: {
    createCollection,
    setCollectionProperties,
    deleteCollectionProperties,
    setCollectionSponsor,
    confirmCollectionSponsorship,
    transferCollection,
    setCollectionLimits,
    addCollectionAdmin,
    removeCollectionAdmin,
    deleteCollection,
    setTokenPropertyPermissions
  }
} satisfies SectionRoutesConfig;