import Axios, {AxiosError, AxiosInstance, AxiosResponse} from 'axios'
import {z} from 'zod'
import qs from 'qs'

import {ApiError} from './errors.js'
import {
  CompactExtrinsicPayload,
  ConditionalEncodeParams,
  ConditionalParams,
  ExtrinsicFeesPayload,
  ExtrinsicFn,
  IAccount,
  IGetExtrinsicStatusOptions,
  IGetExtrinsicStatusOutput,
  SignerPayloadJSON,
  UNIQUE_CHAIN_PARAMS,
  UniqueBuildExtrinsicOptions,
  UnsignedExtrinsicPayload,
  UnsignedExtrinsicPayloadWithCompactExtrinsic,
  zUniqueBuildExtrinsicOptions,
} from './types.js'

// @ts-ignore
z.ZodType.prototype.openapi = function () {
  return this
}

const DEFAULT_TIMES_TO_RETRY = 15
const DEFAULT_TIMEOUT = 3000

const getStatusOptions = (instanceStatusOptions?: IGetExtrinsicStatusOptions, requestStatusOptions?: IGetExtrinsicStatusOptions) => {
  const timeout = requestStatusOptions?.timeout || instanceStatusOptions?.timeout
  const retries = requestStatusOptions?.retries || instanceStatusOptions?.retries

  const TIMEOUT = timeout || DEFAULT_TIMEOUT
  const TIMES_TO_RETRY = retries || DEFAULT_TIMES_TO_RETRY
  const IS_FINALIZED = requestStatusOptions?.isFinalized || instanceStatusOptions?.isFinalized

  return {TIMEOUT, TIMES_TO_RETRY, IS_FINALIZED}
}

const serializeObjectWithBigInt = (obj: unknown) => {
  return JSON.parse(
    JSON.stringify(obj, (key, value) => {
      if (typeof value === 'bigint') return value.toString()
      return value
    }),
  )
}

const getBuildExtrinsic =
  (client: AxiosInstance, chainParams: UNIQUE_CHAIN_PARAMS) =>
  async <P>(
    endpointPart: string,
    paramsValidator: z.ZodSchema<P>,
    _params: P,
    _buildOptions?: UniqueBuildExtrinsicOptions,
  ): Promise<UnsignedExtrinsicPayloadWithCompactExtrinsic> => {
    let buildOptions = {} as UniqueBuildExtrinsicOptions

    if (_buildOptions) {
      const buildOptionsResult = zUniqueBuildExtrinsicOptions.safeParse(_buildOptions)
      if (!buildOptionsResult.success) {
        throw buildOptionsResult.error.format()
      }
      buildOptions = buildOptionsResult.data
    }

    buildOptions.signerAddress = buildOptions?.signerAddress || chainParams.account?.address || ''
    if (!buildOptions.signerAddress) {
      throw new Error('No signer address provided')
    }

    const paramsParseResult = await paramsValidator.safeParseAsync(_params)
    if (!paramsParseResult.success) {
      throw paramsParseResult.error.format()
    }
    const params = paramsParseResult.data

    try {
      const {data: builtExtrinsic} = (await client.post(endpointPart + '/build', {
        args: serializeObjectWithBigInt(params),
        options: buildOptions,
      })) as AxiosResponse<UnsignedExtrinsicPayloadWithCompactExtrinsic>

      return builtExtrinsic
    } catch (e: any) {
      throw new ApiError(e)
    }
  }

const getEncodeExtrinsic =
  (client: AxiosInstance) =>
  async <P>(endpointPart: string, paramsValidator: z.ZodSchema<P>, _params: P): Promise<CompactExtrinsicPayload> => {
    const paramsParseResult = await paramsValidator.safeParseAsync(_params)
    if (!paramsParseResult.success) {
      throw paramsParseResult.error.format()
    }
    const params = paramsParseResult.data

    try {
      const {data: encodeExtrinsic} = (await client.post(endpointPart + '/encode', {
        args: serializeObjectWithBigInt(params),
      })) as AxiosResponse<CompactExtrinsicPayload>

      return encodeExtrinsic
    } catch (e: any) {
      throw new ApiError(e)
    }
  }

const getExtrinsicFee =
  (client: AxiosInstance, chainParams: UNIQUE_CHAIN_PARAMS) =>
  async <P>(endpointPart: string, paramsValidator: z.ZodSchema<P>, _params: P, _buildOptions?: UniqueBuildExtrinsicOptions): Promise<ExtrinsicFeesPayload> => {
    let buildOptions = {} as UniqueBuildExtrinsicOptions

    if (_buildOptions) {
      const buildOptionsResult = zUniqueBuildExtrinsicOptions.safeParse(_buildOptions)
      if (!buildOptionsResult.success) {
        throw buildOptionsResult.error.format()
      }
      buildOptions = buildOptionsResult.data
    }

    buildOptions.signerAddress = buildOptions?.signerAddress || chainParams.account?.address || ''
    if (!buildOptions.signerAddress) {
      throw new Error('No signer address provided')
    }

    const paramsParseResult = await paramsValidator.safeParseAsync(_params)
    if (!paramsParseResult.success) {
      throw paramsParseResult.error.format()
    }
    const params = paramsParseResult.data
    try {
      const {data: extrinsicFees} = (await client.post(endpointPart + '/fee', {
        args: serializeObjectWithBigInt(params),
        options: buildOptions,
      })) as AxiosResponse<ExtrinsicFeesPayload>

      return extrinsicFees
    } catch (e: any) {
      throw new ApiError(e)
    }
  }

const getSignExtrinsic =
  (client: AxiosInstance, chainParams: UNIQUE_CHAIN_PARAMS) =>
  async (payload: UnsignedExtrinsicPayload, account?: IAccount): Promise<string> => {
    const signer = account?.signer || chainParams.account?.signer

    if (!signer) throw new Error('No signer provided')

    return (await signer.sign(payload)).signature
  }

const getSubmitExtrinsic =
  (client: AxiosInstance) =>
  async (extrinsicOrPayload: UnsignedExtrinsicPayload | SignerPayloadJSON, signature: string): Promise<string> => {
    try {
      const {data: sentExtrinsic} = (await client.post('/extrinsics/submit', {
        signerPayloadJSON: 'signerPayloadJSON' in extrinsicOrPayload ? extrinsicOrPayload.signerPayloadJSON : extrinsicOrPayload,
        signature,
      })) as AxiosResponse<{hash: string}>
      return sentExtrinsic.hash
    } catch (e: any) {
      throw new ApiError(e)
    }
  }

const getExtrinsicStatusCheck =
  (client: AxiosInstance, chainParams: UNIQUE_CHAIN_PARAMS) =>
  async <R>(
    endpointPart: string,
    extrinsicHash: string,
    options?: IGetExtrinsicStatusOptions,
  ): Promise<{result: R; extrinsicOutput: IGetExtrinsicStatusOutput}> => {
    let extrinsicStatusResponse: AxiosResponse<{result: R; extrinsic: IGetExtrinsicStatusOutput}> | undefined

    const {TIMEOUT, TIMES_TO_RETRY, IS_FINALIZED} = getStatusOptions(chainParams.statusOptions, options)

    const sleep = (ms: number) => new Promise((resolve) => setTimeout(resolve, ms))

    let counter = 0
    do {
      try {
        let url = endpointPart + '/status/' + extrinsicHash
        if (IS_FINALIZED) url+= '?isFinalized=true'
        extrinsicStatusResponse = await client.get(url)
      } catch (_err) {
        if (!Axios.isAxiosError(_err)) {
          throw _err
        }
        const err = _err as AxiosError

        // console.error(err.response?.status, err.response?.statusText, err.response?.data)
        if (err.response?.status === 404) {
          counter += 1
          if (counter > TIMES_TO_RETRY) {
            throw new Error(`Extrinsic not found in ${((TIMES_TO_RETRY * TIMEOUT) / 1000).toFixed(2)}s`)
          }
          await sleep(TIMEOUT)
        } else {
          throw new ApiError(_err)
        }
      }
    } while (!extrinsicStatusResponse || extrinsicStatusResponse.status !== 200)

    const {data: extrinsicResult} = extrinsicStatusResponse

    return {
      result: extrinsicResult.result,
      extrinsicOutput: extrinsicResult.extrinsic,
    }
  }

export const getExtrinsicOperations = (client: AxiosInstance, chainParams: UNIQUE_CHAIN_PARAMS) => {
  const buildExtrinsic = getBuildExtrinsic(client, chainParams)
  const encodeExtrinsic = getEncodeExtrinsic(client)
  const extrinsicFee = getExtrinsicFee(client, chainParams)
  const signExtrinsic = getSignExtrinsic(client, chainParams)
  const submitExtrinsic = getSubmitExtrinsic(client)
  const getExtrinsicStatus = getExtrinsicStatusCheck(client, chainParams)

  const buildAndSubmitExtrinsic = async <P, R>(
    endpointPart: string,
    paramsValidator: z.ZodSchema<P>,
    params: P,
    buildOptions?: UniqueBuildExtrinsicOptions,
    account?: IAccount,
    getStatusOptions?: IGetExtrinsicStatusOptions,
  ): Promise<{result: R; extrinsicOutput: IGetExtrinsicStatusOutput}> => {
    const builtExtrinsic = await buildExtrinsic(endpointPart, paramsValidator, params, buildOptions)
    const signature = await signExtrinsic(builtExtrinsic, account)
    const extrinsicHash = await submitExtrinsic(builtExtrinsic, signature)

    return getExtrinsicStatus<R>(endpointPart, extrinsicHash, getStatusOptions)
  }

  const extrinsic = {
    build: buildExtrinsic,
    encode: encodeExtrinsic,
    fee: extrinsicFee,
    sign: signExtrinsic,
    submit: submitExtrinsic,
    getStatus: getExtrinsicStatus,
    invoke: buildAndSubmitExtrinsic,
  }

  const makeExtrinsicFn = <P, K, R>(extrinsicSettings: {
    route: string
    schemaIn: z.ZodType<any, z.ZodTypeDef, P>
    encodeSchemaIn: z.ZodType<any, z.ZodTypeDef, K>
    schemaOut: z.ZodType<R, z.ZodTypeDef, any>
  }): ExtrinsicFn<P, K, R> => {
    const {route, schemaIn, schemaOut} = extrinsicSettings

    const fn = async (...args: ConditionalParams<P>) => {
      const [params, buildOptions, account, options] = args
      return buildAndSubmitExtrinsic<P, R>(route, schemaIn, params as P, buildOptions, account, options)
    }

    fn.buildAndSubmit = async (...args: ConditionalParams<P>) => {
      const [params, buildOptions, account, options] = args
      return buildAndSubmitExtrinsic<P, R>(route, schemaIn, params as P, buildOptions, account, options)
    }

    fn.route = route
    fn.schemaIn = schemaIn
    fn.schemaOut = schemaOut

    fn.build = async (...args: ConditionalParams<P>) => {
      const [params, buildOptions] = args
      return buildExtrinsic(route, schemaIn, params, buildOptions)
    }

    fn.encode = async (...args: ConditionalEncodeParams<K>) => {
      const [params] = args
      return encodeExtrinsic(route, schemaIn, params)
    }

    fn.fee = async (...args: ConditionalParams<P>) => {
      const [params, buildOptions] = args
      return extrinsicFee(route, schemaIn, params, buildOptions)
    }

    fn.getStatus = getExtrinsicStatus<R>

    fn.sign = signExtrinsic
    fn.submit = submitExtrinsic

    return fn
  }

  return {extrinsic, makeExtrinsicFn}
}

export const getQueryFn =
  (client: AxiosInstance) =>
  <P, R>(settings: {route: string; method?: 'GET' | 'POST'; schemaIn: z.ZodSchema<P>; schemaOut: z.ZodSchema<R>}) => {
    const {route, schemaIn, schemaOut, method = 'GET'} = settings

    const fn = async (params: P): Promise<R> => {
      const paramsParseResult = schemaIn.safeParse(params)
      if (!paramsParseResult.success) throw paramsParseResult.error.format()
      const parsedParams = paramsParseResult.data

      try {
        const response = method === 'GET' ? await client.get(route, {params: parsedParams}) : await client.post(route, serializeObjectWithBigInt(parsedParams))

        return response.data
      } catch (e: any) {
        throw new ApiError(e)
      }
    }

    fn.route = route
    fn.schemaIn = schemaIn
    fn.schemaOut = schemaOut

    return fn
  }

const createAxiosClient = (chainParams: UNIQUE_CHAIN_PARAMS) =>
  Axios.create({
    ...chainParams.axiosConfig,
    baseURL: chainParams.baseUrl,
    paramsSerializer: (params) => qs.stringify(params, {arrayFormat: 'repeat'}),
  })

export const prepareBase = (options: UNIQUE_CHAIN_PARAMS) => {
  const client = createAxiosClient(options)

  const makeQueryFn = getQueryFn(client)
  const {extrinsic, makeExtrinsicFn} = getExtrinsicOperations(client, options)

  return {client, makeQueryFn, extrinsic, makeExtrinsicFn}
}

export const zAlternativeBatchArgs = z
  .array(
    z.promise(
      z.object({
        compactExtrinsic: z.string(),
      }),
    ),
  )
  .transform(async (promises) => {
    const calls = await Promise.all(promises.map((call) => call.then((c: any) => c.compactExtrinsic)))

    return {calls}
  })
